<template>
   <div style="margin-bottom:5px;" >
        <div v-if="secondDropdown == null" id="buttons" v-intro-step="1" v-intro="'Use the dropdown menu to select different maps for display.'">
            <div class="dropdown">
                <button class="dropbtn"><b id="dataType">{{dropdownText}}</b><p id="arrow">&#9660;</p></button>
                <div class="dropdown-content" style="z-index:1500;">
                    <div v-if="dropdownLabel">
                        <div v-if="dropdownLabel.type == 'top'">
                            <p v-for="label in dropdownLabel.labels" v-bind:key="label.index"  id="dropdownLabel">{{label}}</p>
                        </div>
                    </div>
                    <div v-for="(item,index) in maps" v-bind:key="item.id" style="display:inline;">
                        <div v-if="dropdownLabel" style="display:inline;" v-bind:style="{display:dropdownLabelBreak(index, dropdownLabel.breaks)}">
                            <div v-if="dropdownLabel.type == 'breaks'">
                                <div v-for="(breaks, key) in dropdownLabel.breaks" v-bind:key="breaks.key">
                                    <p v-if="breaks == index" id="dropdownLabelCentered">{{dropdownLabel.labels[key]}}</p>
                                </div>
                            </div>
                        </div>
                        <button v-on:click="selected=`map-${item.id}`; select()"
                                v-bind:style="{fontSize:fontSize(item.id, item.title)}"
                                type="button" 
                                v-bind:id="`map-${item.id}`" 
                                class="selection">
                                {{item.title}}</button>
                    </div>
                </div>
            </div>
        </div>
        <div v-else id="buttons">
            <div v-if="secondDropdown.type == null">
                <div class="dropdownSmall" v-intro-step="1" v-intro="'Use this dropdown menu to select a filter for the map.'">
                    <button class="dropbtnSmall"><b id="dataType2">{{secondDropdown.title}}</b><p id="arrow">&#9660;</p></button>
                    <div class="dropdown-contentSmall" style="z-index:1500;">
                        <button v-for="(item, index) in secondDropdown.buttons" 
                                v-bind:key="item.index" 
                                v-on:click="secondSelected=`filter-${index}`; selectFilter()"
                                v-bind:style="{fontSize:fontSize(index, item.label)}"
                                type="button" 
                                v-bind:id="`filter-${index}`" 
                                class="selectionSmall" >
                                {{item.label}}</button>
                    </div>
                </div>
                <div class="dropdownSmall" style="margin-left:10px;"  v-intro-step="2" v-intro="'Use this dropdown menu to select different maps for display.'">
                    <button class="dropbtnSmall"><b id="dataType">{{dropdownText}}</b><p id="arrow">&#9660;</p></button>
                    <div class="dropdown-contentSmall" style="z-index:1500;">
                        <button v-for="item in maps" 
                                v-bind:key="item.id" 
                                v-on:click="selected=`map-${item.id}`; select()"
                                v-bind:style="{fontSize:fontSize(item.id, item.title)}"
                                type="button" 
                                v-bind:id="`map-${item.id}`" 
                                class="selectionSmall" >
                                {{item.title}}</button>
                    </div>
                </div>
            </div>
            <div v-if="secondDropdown.type == 'spyglass'">
                <div class="dropdownSmall" v-intro-step="1" v-intro="'Use this dropdown menu to change the basemap.'">
                    <button class="dropbtnSmall"><b id="dataType">{{dropdownText}}</b><p id="arrow">&#9660;</p></button>
                    <div class="dropdown-contentSmall" style="z-index:1500;">
                        <button v-for="item in maps" 
                                v-bind:key="item.id" 
                                v-on:click="selected=`map-${item.id}`; select()"
                                v-bind:style="{fontSize:fontSize(item.id, item.title)}"
                                type="button" 
                                v-bind:id="`map-${item.id}`" 
                                class="selectionSmall" >
                                {{item.title}}</button>
                    </div>
                </div>
                <div class="dropdownSmall" style="margin-left:10px;" v-intro-step="2" v-intro="'Use this dropdown menu to change the spyglass.'">
                    <button class="dropbtnSmall"><b id="dataType2">{{secondDropdown.title}}</b><p id="arrow">&#9660;</p></button>
                    <div class="dropdown-contentSmall" style="z-index:1500;">
                        <button v-for="item in maps.slice(1)" 
                                v-bind:key="item.id" 
                                v-on:click="spyglassSelected=`spy-${item.id}`; selectSpyglass()"
                                v-bind:style="{fontSize:fontSize(item.id, item.title)}"
                                type="button" 
                                v-bind:id="`spy-${item.id}`" 
                                class="selectionSmall" >
                                {{item.title}}</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>


export default {
  name:'MapDropdown',
  data() {
    return {
        selected:'map-0',
        secondSelected:'filter-0',
        spyglassSelected:'spy-1'
    }
 },
  props:{
      maps:Array,   
      dropdownText:String,
      mapDiv:String,
      secondDropdown:Object,
      dropdownLabel:Object
  },
  mounted() {
      document.getElementById(this.selected).style.background = "rgba(242, 242, 242, 0.8)";
      if (document.getElementById(this.secondSelected))
        document.getElementById(this.secondSelected).style.background = "rgba(242, 242, 242, 0.8)";
      if (document.getElementById(this.spyglassSelected))
        document.getElementById(this.spyglassSelected).style.background = "rgba(242, 242, 242, 0.8)";
  },
  methods:{
      select(event){
          var mapID = [];
          var selectedID;
          for(var i = 0; i < this.maps.length; i++){
            mapID[i]="map-" + i;
            if (this.selected == mapID[i]){
                document.getElementById(mapID[i]).style.background = "rgba(242, 242, 242, 0.8)";
                document.getElementById('dataType').innerHTML = this.maps[i].title;
                selectedID = i;
            }
            else
                document.getElementById(mapID[i]).style.background = "rgba(242, 242, 242, 0.2)";
            }
          this.$emit('clicked', selectedID);
      },
      selectFilter(event){
          var filterID = [];
          var selectedFilter;
          for(var i = 0; i < this.secondDropdown.buttons.length; i++){
            filterID[i]="filter-" + i;
            if (this.secondSelected == filterID[i]){
                document.getElementById(filterID[i]).style.background = "rgba(242, 242, 242, 0.8)";
                document.getElementById('dataType2').innerHTML = this.secondDropdown.buttons[i].label;
                selectedFilter = this.secondDropdown.buttons[i].filter;
            }
            else
                document.getElementById(filterID[i]).style.background = "rgba(242, 242, 242, 0.2)";
            }
          this.$emit('filterClicked', selectedFilter);
      },
      selectSpyglass(event){
          var spyID = [];
          var selectedSpyglass;
          for(var i = 1; i < this.maps.length; i++){
            spyID[i]="spy-" + i;
            if (this.spyglassSelected == spyID[i]){
                document.getElementById(spyID[i]).style.background = "rgba(242, 242, 242, 0.8)";
                document.getElementById('dataType2').innerHTML = this.maps[i].title;
                selectedSpyglass = i;
            }
            else
                document.getElementById(spyID[i]).style.background = "rgba(242, 242, 242, 0.2)";
            }
          this.$emit('spyglassClicked', selectedSpyglass);
      },
      fontSize(id, text){
          var divId = "#map-" + id;
          var length = text.length;
          if (length > 18)
            return '12px'
          else
            return '14px';
      },
      dropdownLabelBreak(index, value){
        var display;
        for (var i = 0; i < value.length; i++){
            if (value[i] == index){
                display = "block";
                break;
            }
            else 
                display = "none";
        }
        return display;
      }
  },
  watch:{
      '$route':function(){
          this.selected = "map-0";
          this.select();
          document.getElementById("dataType").innerHTML = this.dropdownText;
      }
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
div#buttons p {
	width:135px;
	height:25px;
	font-weight:bold;
	background: rgba(255,255,255,0);
	border-style: solid;
    border-color: rgba(255,255,255,0);
    text-align:center;
    vertical-align:middle;
    margin-left:2px;
    position:relative;
    padding:0;
    margin-top:5px;
    border:0;
}
div#buttons b {
	color:#4d4d4d; 
	float:left; 
	display:inline; 
	padding:0; 
	width:auto; 
	font-size:18px; 
	top:2px;
	position:relative;
	font-weight:normal;
	height:25px;
}
div#buttons #arrow {
	color:#e6e6e6; 
	text-align:right; 
	display:inline; 
	padding:0; 
	width:auto; 
	font-size:16px; 
	padding-top:4px;
	right:9px;
	position:absolute;
	margin-top:0px;
}
div#buttons .selection {
	width:135px;
	height:30px;
	background: rgba(242,242,242,0.2);
	border-style: solid;
    border-color: rgba(255,255,255,0);
    margin-bottom:2px;
    margin-left:2px;
    margin-right:3px;
    font-family:GoudyOld;
    font-size:14px;
    overflow: hidden;
}
div#buttons .selection:hover{
	background: rgba(242, 242, 242, 1) !important;
	cursor:pointer;

}
.dropbtn {
    background: #fff;
	border-radius:.125em;
	box-shadow: 0 0 4px #999;
    font-size:18px;
    border: none;
    cursor: pointer;
    width:280px;
    border-style: solid;
    border-color: rgba(0,0,0,1);
    border-width:2px;
    font-family:GoudyOld;
    text-align:left;
    color:#4d4d4d;
    height:35px;
}
.dropdown {
    position: relative;
    display: inline-block;
}
.dropdown-content {
    display: none;
    position: absolute;
   background: #ffffff;
    width: 280px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
.dropdown:hover .dropdown-content {
    display: block;
}
.dropdown:hover .dropbtn {
     background: rgba(242, 242, 242, 0.67);
     -webkit-box-shadow: 0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	-moz-box-shadow:    0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	box-shadow:         0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	cursor:pointer;
}

/*dropdown small*/
div#buttons .selectionSmall {
	width:130px;
	height:30px;
	background: rgba(242,242,242,0.2);
	border-style: solid;
    border-color: rgba(255,255,255,0);
    margin-bottom:2px;
    margin-left:2px;
    font-family:GoudyOld;
    font-size:14px;
}
div#buttons .selectionSmall:hover {
	width:130px;
	background: rgba(242, 242, 242, 1) !important;
	cursor:pointer;
}
.dropbtnSmall {
    background: #fff;
	border-radius:.125em;
	box-shadow: 0 0 4px #999;
    font-size: 16px;
    border: none;
    cursor: pointer;
    width:135px;
    border-style: solid;
    border-color: rgba(0,0,0,1);
    border-width:2px;
    font-family:GoudyOld;
    text-align:left;
    color:#4d4d4d;
    height:32px;
}

/* The container <div> - needed to position the dropdown content */
.dropdownSmall {
    position: relative;
    display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.dropdown-contentSmall {
    display: none;
    position: absolute;
   background: #ffffff;
    width: 135px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}
/* Show the dropdown menu on hover */
.dropdownSmall:hover .dropdown-contentSmall {
    display: block;
}
/* Change the background color of the dropdown button when the dropdown content is shown */
.dropdownSmall:hover .dropbtnSmall {
     background: rgba(242, 242, 242, 0.67);
     -webkit-box-shadow: 0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	-moz-box-shadow:    0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	box-shadow:         0px 0px 10px 0px rgba(191, 191, 191, 0.67);
	cursor:pointer;
}

p#dropdownLabel{
    margin-bottom:0px;
    display:inline-block;
}
p#dropdownLabelCentered{
    margin-bottom:0px;
    left:70px;
    width:270px;
    text-align:center;
    display:inline-block;
}
</style>
