<template>
  <div class="top" id="topNavbar">
        <a href="/" class="logo" style="display:inline;">{{title}}</a>
        <div class="right">
			<a class="links" @click="openPopup = true;">Chapters</a>
			<a class="links" href="/">Home</a>
        </div>
		<ChapterPopup v-if="openPopup == true" @close="closePopup()"/>
  </div>
</template>

<script>
import ChapterPopup from './ChapterPopup.vue'

export default {
  name: 'MapNavbar',
  components:{
	ChapterPopup  
  },
  data() {
    return {
            openPopup:false,
        }
	},
  mounted(){
        let self = this;
        window.addEventListener('click', function(event){
            if (!$(event.target).closest("#chapters, #topNavbar").length) {
                self.closePopup(false);
            }
        });
    },
  props:{
	  title:String,
  },
  methods:{
      closePopup(value){
          this.openPopup = false;
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div.top{
    font-family:GoudyOld;
    top:0;
	left:0;
	position:fixed;
	height:34px;
	background:black;
	width:100%;
	z-index:1500;
	color:white;
}
div.right{
    position:absolute;
    right:10px;
}
a{
	font-size:24px;
	color: white;
	top:2px;
	text-decoration:none;
}
a.logo{
	text-align:left;
	position:absolute;
	font-weight:bold;
	left:10px;
}
a.logo:hover{
    cursor:pointer;
    color:#4d4d4d;	
}
a.links{
	text-align:right;
	position:relative;
	font-weight:normal;
    margin-left:40px;
}
a.links:hover{
	color:#4d4d4d;	
	cursor:pointer;
}
</style>
