<template>
   <div id="InfoPanel">
         <ToolTip v-if="tooltip.tooltip == true" v-bind:tooltip="tooltip.tooltipText" v-bind:position="tooltip.tooltipPos"/>
         <TopTitle v-bind:title="data.title" v-bind:chapter="data.chapter" v-bind:URL="data.URL"/>
         <MapDropdown v-if="data.maps.length > 1" v-bind:maps="data.maps" v-bind:secondDropdown="data.secondDropdown" v-bind:dropdownLabel="data.dropdownLabel" v-bind:dropdownText="data.dropdownText" v-bind:selected="selected" @clicked="dropdownChange" @filterClicked="secondDropdownChange" @spyglassClicked="spyglassClicked"/>
         <LayerCheckbox v-if="data.checkboxLayer" v-bind:checkbox="data.checkboxLayer" @checked="checkLayer" v-intro-step="3" v-intro="'Toggle to display overlay layer.'"/>
         <ResetButton v-if="data.maps[selected].featureSelected == true" @reset="resetSelection"/>
         <VuePerfectScrollbar class="info-scroll"  v-intro-step="4" v-intro="'Each dataset has a specific legend, displayed here.'">
            <LegendContainer v-bind:legendData="data.maps[selected]" v-bind:chapter="data.chapter" v-bind:secondSelection="secondSelection" v-bind:checkLayer="data.checkboxLayer" v-bind:sliderValue="sliderValue" v-bind:isChecked="check" v-bind:initial="selected" v-bind:spyglassID="spyglassID" v-bind:spyglass="data.spyglass" v-bind:borders="borders" v-bind:spyglassLegend="data.maps[spyglassID]" @tooltipHover="tooltipHover" @toggled="toggled" />
         </VuePerfectScrollbar>
         <BottomRow v-if="data.sources != null" v-bind:sources="data.sources" v-bind:chapterMaps="chapterMaps"/>
         <img v-bind:src="getImage('library_inr_logo')" style="height:40px; position:absolute; bottom: 5px;"/>
         <TimelineSlider v-if="data.maps[selected].slider" v-bind:slider="data.maps[selected].slider" @sliderChange="sliderChange" v-intro-step="10" v-intro="'Select a year to view data in that year.'"/>
   </div>
</template>

<script>

import TopTitle from './TopTitle.vue'
import MapDropdown from './MapDropdown.vue'
import LegendContainer from './LegendContainer.vue'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import BottomRow from './BottomRow.vue'
import TimelineSlider from './TimelineSlider.vue'
import ResetButton from './ResetButton.vue'
import ToolTip from './ToolTip.vue'
import LayerCheckbox from './LayerCheckbox.vue'


export default {
  name:'InfoPanel',
  props:{
      data:Object,
      selected:Number,
      chapterMaps:Object,
      borders:Boolean
  },
  components:{
      TopTitle,
      MapDropdown,
      LegendContainer,
      VuePerfectScrollbar,
      BottomRow,
      TimelineSlider,
      ResetButton,
      ToolTip,
      LayerCheckbox
  },
  data() {
    return {
        tooltip:{
            tooltip:false,
            tooltipPos:null,
            tooltipText:null 
        },
        secondSelection:0,
        check:false,
        spyglassID:1,
        sliderValue:null    
    }
  },
  mounted(){
      this.resizeMiddle();
      window.addEventListener('resize', this.resizeMiddle);
      
      if (this.data.infoBackground)
          $('#InfoPanel').css('background-color',this.data.infoBackground);
  },
  updated(){
      this.resizeMiddle();
  },
  methods:{
    dropdownChange(value){
        this.$emit('clicked', value);
    },
    getImage(img){
        var images = require.context('../../assets/img/', true, /\.png$/)
        return images('./' + img + ".png")
    },
    secondDropdownChange(value){
        for (var k = 0; k < this.data.secondDropdown.buttons.length; k++){
            if (this.data.secondDropdown.buttons[k].filter == value)
                this.secondSelection = k;
        }
        this.$emit('filterClicked', value);
    },
    spyglassClicked(value){
        this.spyglassID = value;
        this.$emit('spyglassClicked', value);
    },
    sliderChange(value){
        this.sliderValue = value;
        this.$emit('sliderChange', value);
    },
    resetSelection(){
        this.$emit('resetSelection');
    },
    checkLayer(value){
        this.check = value;
        this.$emit('checked', value);
    },
    tooltipHover(hover, text, position){
        this.tooltip.tooltip = hover;
        this.tooltip.tooltipPos = position;
        this.tooltip.tooltipText = text;
    },
    resizeMiddle() {
    var minHeight = 200,
    dropdownHeight = document.getElementById("buttons") != null ? $('#buttons').height() : 0,
    checkBox = document.getElementById("toggle") != null ? 40 : 0,
    resetHeight = document.getElementById("resetButton") != null ? 50 : 0,
    h = $('body').height() - $('#top').height() - dropdownHeight - checkBox - resetHeight - 145;
        h = h > minHeight ? h : minHeight;
    $('.info-scroll').height(h);
    },
    toggled: function(value){
        this.$emit('toggled', value);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
div#InfoPanel{
    width:300px;
    height:100%;
    height:auto;
    background-color: rgba(255,255,255,0.6);
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    z-index:1000; 
    padding-left:20px;
    padding-top:35px;
    font-family:GoudyOld;
    display:block;
    }
.info-scroll{
    left:0;
    z-index:1;
    margin:0;
}

</style>
