<template>
    <div id="share" class="shareOverlay">
			<div class="sharePopup">
				<button id="clear" style="display:none;">×</button>
				<div class="content">
					<a href="" id="fb_share" style="display:inline-block;">
						<i class="fa fa-facebook-square " aria-hidden="true" style="padding-left:0;"></i>
					</a>    
					<a href="" id="tw_share" style="display:inline-block;">
						<i class="fa fa-twitter-square " aria-hidden="true" style="padding-left:0;"></i>
					</a>
				</div>
			</div>
		</div>
</template>

<script>
export default {
  name: 'SharePopup',
  props:{
      share:Object
  },
  mounted(){
      fb_share.href ='https://www.facebook.com/sharer/sharer.php?u=' + encodeURIComponent(location.href); 
      tw_share.href="https://twitter.com/share?url=" + encodeURIComponent(location.href);
  },
  methods:{
    close(){
         this.$emit('close', false);
      }
  }
}
</script>
    
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
 .shareOverlay {
  position: fixed;
  bottom: 45px;
  left: 120px;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: visible;
  opacity: 1;
  z-index:1000;
  width:80px;
}
.sharePopup {
  padding:5px;
  background: #fff;
  width: 100%;
  position: relative;
  transition: all 5s ease-in-out;
  font-family:GoudyOld;
  height:100%;
  box-shadow: 3px 3px 2px #4d4d4d;
  border:2px black solid;
}
.sharePopup a#fb_share, .sharePopup a#tw_share {
	color:black;
	font-size:36px;
	position:relative;
	margin-left:5px;
}
.sharePopup a#fb_share:hover, .sharePopup a#tw_share:hover {
	color:#808080;
	cursor:pointer;
}
</style>
