<template>
  <div id="home">
    <VuePerfectScrollbar class="scroll-area" id="main-scrollbar" v-once :settings="settings">
        <TopNavbar type="home" title="OSU Libraries & Press | Institute for Natural Resources" v-bind:links="[{title:'Chapters', link:'#chapters'},{title:'About', link:'#about'},{title:'Contributors', link:'#authors'}]"/>
        <ScrollArrow/>
        <MainTitle main="Atlas of the Pacific Northwest" secondary="2019 Edition"/>
        <div id="content">
          <SectionTitle title="Chapters" id="chapters" style="padding-top:75px;"/>
          <div style="width:80%;">
              <ChapterList   
                  v-for="item in chapters"
                  v-bind:chapter="item"
                  v-bind:key="item.id">
              </ChapterList>
          </div>
          <SectionTitle title="About" id="about" style="padding-top:75px; margin-top:75px;"></SectionTitle>
              <AboutSection
                    v-for="item in about"
                    v-bind:about="item"
                    v-bind:key="item.id"> 
              </AboutSection>
          <SectionTitle title="Contributors" id="authors" style="padding-top:75px; margin-top:75px;"></SectionTitle>
              <ContributorList
                    v-for="item in contributors"
                    v-bind:contributor="item"
                    v-bind:key="item.id"> >
              </ContributorList>
            <div style="padding:50px 0px 100px;">
                  <ImageBanner
                      v-for="item in banners"
                      v-bind:image="item"
                      v-bind:key="item.id"> >
                  </ImageBanner>
              </div>
        </div>
    </VuePerfectScrollbar>
  </div>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import MainTitle from './components/MainTitle.vue'
import TopNavbar from './components/TopNavbar.vue'
import ScrollArrow from './components/ScrollArrow.vue'
import SectionTitle from './components/SectionTitle.vue'
import ChapterList from './components/ChapterList.vue'
import AboutSection from './components/AboutSection.vue'
import ContributorList from './components/ContributorList.vue'
import ImageBanner from './components/ImageBanner.vue'

export default {
  name: 'home',
  components: {
    MainTitle,
    TopNavbar,
    ScrollArrow,
    SectionTitle,
    VuePerfectScrollbar,
    ChapterList,
    AboutSection,
    ContributorList,
    ImageBanner
  },
  mounted(){
      document.title = "Atlas of the Pacific Northwest";
      $('meta[property="og:title"]').remove();
      $('head').append( '<meta property="og:title" content="">');
      $("meta[property='og:title']").attr("content", "Atlas of the Pacific Northwest:");
  },
  data() {
    return {
      settings: {
        maxScrollbarLength: 1000,
        scrollXMarginOffset:1000
      },
      chapters: [
        { 
            id:0,
            text: 'Agriculture & Food' ,
            description:'Alcohol production, crops, food access, livestock',
            imageURL:"food_icon",
            link:"/food",
            active:false,
            backgroundURL:"access_background1"},
        { 
            id:1,
            text: 'Culture & History' ,
            description:'Indigenous history, Euro-American settlement, historical maps, land seizure, tribal lands',
            imageURL:"history_icon",
            link:"/history",
            active:false,
            backgroundURL:"history_background1"},
        { 
            id:2,
            text: 'Energy Resources' , 
            description:'Electric utilities, power plants, renewable energy',
            imageURL:"energy_icon",
            link:"/energy",
            active:false,
            backgroundURL:"utilities_background1"},
        { 
            id:3,
            text: 'Marine' , 
            description:'Conservation, estuaries, fisheries, marine mammals, ocean economy, ocean health',
            imageURL:"marine_icon",
            link:"/marine",
            active:false,
            backgroundURL:"marine_background"},
        { 
            id:4,
            text: 'Natural Hazards' ,
            description:'Drought, Earthquakes, extreme weather, flooding, hazard risk, volcanoes, wildfires',
            imageURL:"hazard_icon",
            link:"/hazards",
            active:false,
            backgroundURL:"earthquake_background1"
            },
        { 
            id:5,
            text: 'Outdoor Recreation' ,
            description:'Hunting, protected lands, outdoor activities, outdoor economy',
            imageURL:"recreation_icon",
            link:"/recreation",
            active:false,
            backgroundURL:"park_background1"},
        { 
            id:6,
            text: 'Population & Housing' ,
            description:'Demographics, housing,immigration, internal migration, urban/rural',
            imageURL:"housing_icon",
            link:"/population",
            active:false,
            backgroundURL:"demographics_background1"},
        { 
            id:7,
            text: 'Transportation' ,
            description:'Air travel, commutes, highways and roads, public transit, railroads',
            imageURL:"transportation_icon",
            link:"/transportation",
            active:false,
            backgroundURL:"transportation_background"},
        { 
            id:8,
            text: 'Water' ,
            description:'Groundwater, watersheds, water pollution, water use',
            imageURL:"water_icon",
            link:"/water",
            active:false,
            backgroundURL:"watershed_background1"},
        { 
            id:9,
            text: 'Wildlife',
            description:'Fauna, fish, flora, invasive species',
            imageURL:"wildlife_icon",
            link:"/wildlife",
            active:false,
            backgroundURL:"flora_background1"}
        ],
      about:[
                {
                    paragraph:'The Pacific Northwest is a tapestry of landscapes. Major metropolitan areas abut forests, rivers, and bays—overlooked by mountains and deserts. Small towns dot fertile valleys, nestled between reservoirs and waterfalls. Few places can match this scenic diversity. Any visual representation of the region must attempt to do so.',
                },
                {
                    paragraph:'Since 1953, the <i>Atlas of the Pacific Northwest</i> has been an invaluable reference for Northwesterners and newcomers alike—an authoritative portrait of the region. Nine editions have been released by OSU Press, the most recent in 2003 (currently out of print). This 2018 edition is the first to be released in a digital, fully-interactive format, designed to highlight facets of the Pacific Northwest landscape with novel approaches to data presentation. Where previous editions of the atlas were designed to ask and answer questions, this atlas serves as a platform for the geographically curious to explore the region, providing as many critical questions as it does critical answers.',
                },
                {
                    paragraph:'Beyond this page are maps of the familiar and the unfamiliar. Migration maps highlight human movement between the Pacific Northwest and the rest of the United States; a wildfire timeline chronicles the year-to-year spread of modern and historical fires; and the watershed guide abandons traditional political boundaries in favor of natural, hydrological borders. All data in the atlas were gathered from publically accessible sources, compiled using open-source software and coding libraries. This is an atlas designed to be open, responsive, and to satisfy the geographic curiosity of any and all interested.',
                },
                {
                    paragraph:'If you have questions, please contact Janine Salwasser at janine.salwasser@oregonstate.edu.',
                    break:true
                }
            ],
      contributors:[
                {
                    name:'Gareth Baldrica-Franklin',
                    about:'Lead Designer/Cartographer',
                },
                {
                    name:'Jillian Pihulak',
                    about:'Cartographer',
                },
                {
                    name:'Korey Jackson',
                    about:'Project Manager - OSU Libraries and Press',
                },
                {
                    name:'Janine Salwasser',
                    about:'Program Coordinator - Institute for Natural Resources',
                },
                {
                    name:'Marc Rempel',
                    about:'Technical Program Manager - OSU Libraries and Press',
                },
                {
                    name:'Special Thanks To:',
                    about:'Faye Chadwell, Lisa Gaines, Tom Booth, Bo Zhao, Mary Braun, Tanya Haddad (Marine), Deana Caracciolo (Marine), Adrian Laufer (Marine), Charles Robinson (Outdoor Recreation), Brett Lucas (Transportation) Todd Jarvis (Water), and Jimmy Kagan (Wildlife).',
                }
            ],
        banners:[
                {
                    URL:"osulp_logo",
                },
                {
                    URL:"inr_logo",
                },
            ]
    }
  }
}
</script>

<style>
@keyframes slideInFromRight {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

div#home {
  font-family: GoudyOld;
  height:100%;
  left:0;
  width: 100%;
  z-index:1;
  background:url(./assets/img/pnw_boundary.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position:70% 50%;
  	-webkit-animation: 1s ease-out 0s 1 slideInFromRight;  
	-ms-animation: 1s ease-out 0s 1 slideInFromRight;
	animation: 1s ease-out 0s 1 slideInFromRight;
}
.scroll-area{
  height:100%;
  position:relative;
  left:0;
  width: 50%;
  z-index:1;
}
#content{
  width:100%;
  height:100%;
  margin:0 auto;
  padding:0;
  border-radius: 10px;
  padding-left:20%;
  padding-bottom:50px;
  left:20%;
}
</style>
