<template>
  <div id="frontCover" class="back">
		<h4 class="main">{{ main }}</h4>
		<p class="secondary">{{ secondary }}</p>
	</div>
</template>

<script>
export default {
  name: 'MainTitle',
  props: {
    main: String,
    secondary:String
  },
  methods: {
        position(){
            var height =  ($('.back').height()/2) - $('.main').height()-40;
		    $('.main').css('top', height);
		    $('.secondary').css('margin-top', height);
        }
    },
  mounted() {
        $(document).ready(this.position);
        $(window).on('load', this.position);
	    $(window).scroll(this.position);
	    $(window).resize(this.position);
    }    
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    h4{
        display:inline-block;
        font-size:5.5vw;
        text-align:left;
        margin-bottom:10px;
        position:relative;
    }
    p{
        font-size:2vw; 
        position:relative;
        margin-top:15px;
    }
    div#frontCover{
        margin:0;
        padding:0;
        top:0;
        padding-left:20%;
        background: rgba(255, 255, 255, 0);
        background-size: 100% auto;
        height:100%;
    }
</style>
