<template>
   <div id="legend" style="width:280px; margin-top:10px;">
        <!--spyglass legend-->
        <div v-if="spyglass">
            <p class="description" style="margin-bottom:15px">Use the dropdowns from above to select a background map, and a spyglass map, which will follow your cursor around the page.</p>
            <h2 class="mainTitle">Spyglass Layer</h2>
            <h2 v-if="spyglassLegend.legend[1].title">{{spyglassLegend.legend[1].title}}</h2>
            <div>
                <div v-for="item in spyglassLegend.legend[1].containers" v-bind:key="item.index" style="margin-bottom:10px;">
                    <p class="list" style="font-weight:bold;">{{item.labelTitle}}</p>
                    <p class="list" style="font-weight:normal;">{{item.label}}</p>
                </div>
            </div>
        </div>
        <!--normal legend layer-->
        <div>
            <div v-for="(legend,index) in legendData.legend" v-bind:key="legend.index" v-bind:class="legend.legendDisplay != null ? legend.legendDisplay : []" v-bind:style= "[legend.legendDisplay != null && legend.legendDisplay == 'selected' ? {'display':'none'} : {'display':'block'}]">
                <!--legend dependant on values in timeline slider-->
                <div v-if="legend.sliderValue" style="margin-bottom:20px;">
                    <div v-if="legend.sliderValue[0] <= sliderValue && legend.sliderValue[1] >= sliderValue">
                        <h2 v-bind:id="[legend.type == 'chart' ? 'chartTitle' : 'title-'+index]" v-if="legend.title != null && legend.type != 'selection' && legend.type != 'selectionChartOnly' && legend.type != 'selectionChart' && legend.type != 'pointSizeColor' && legend.type != 'polyLine' && legend.type != 'polyGrid'" v-bind:style="[legend.type === 'point' || legend.type === 'pointRow' || legend.type === 'doubleLine' || legend.type === 'line' ? {'margin-bottom': '5px'} : legend.subtitle != null ? {'margin-bottom': '0px'} : {'margin-bottom': '10px'}]">{{legend.title}}</h2>
                        <div class='point' v-for="(item,index) in legend.containers" v-bind:key="item.index">
                            <p class="colorLabel" v-bind:style="{ marginLeft:item.margin + 'px'}">{{item.label}}</p>
                            <b class="circle" v-bind:id="'circle-' + index" v-bind:style="{background: item.background, width: item.size + 'px', height:item.size + 'px', marginTop:pointHeight(item.size),borderColor:item.color, borderStyle:'solid', borderWidth:item.border != null ? item.border + 'px': '2px'}" style="width:20px; height:20px;margin-left:0;"></b>
                        </div>
                    </div>
                </div>
                <div v-else-if="legend.secondDropdownLegend == null || legend.id == secondSelection" v-bind:style="legend.bottomMargin != null ? {'margin-bottom':'0px'} : {'margin-bottom':'20px'}" style="margin-bottom:20px; position:relative;">
                    <!--icon at the top of the legend-->
                    <img class="topIcon" v-if="legend.topIcon" v-bind:src="getURL(legend.topIcon)" v-bind:style="[legend.topIconWidth != null ? {'width':legend.topIconWidth + 'px', 'margin-left':bottomIconMargin(legend.topIconWidth)} : {'width':'250px','margin-left':bottomIconMargin(legend.topIconWidth)}]">
                    <!--line above legend title-->
                    <hr v-if="legend.topLine == true" style="border:1px solid #e6e6e6; border-bottom-width:0px;">
                    <!--large title in legend (larger than normal title)-->
                    <h2 v-if="legend.mainTitle" class="mainTitle">{{legend.mainTitle}}</h2>
                    <!--legend title-->
                    <h2 v-bind:id="[legend.type == 'chart' ? 'chartTitle' : 'title-'+index]" v-if="legend.title != null && legend.type != 'selection' && legend.type != 'selectionChartOnly' && legend.type != 'selectionChart' && legend.type != 'pointSizeColor' && legend.type != 'polyLine' && legend.type != 'polyGrid'" v-bind:style="[legend.type === 'point' || legend.type === 'pointRow' || legend.type === 'doubleLine' || legend.type === 'line' ? {'margin-bottom': '5px'} : legend.subtitle != null ? {'margin-bottom': '0px'} : {'margin-bottom': '10px'}]">{{legend.title}}</h2>
                    <!--legend subtitle-->
                    <p class="subtitle" v-bind:id="'mainSubtitle-'+index" v-if="legend.subtitle != null && legend.type != 'selection'&& legend.type != 'selectionChart' && legend.type != 'pointSizeColor' && legend.type != 'polyGrid'" v-bind:style="{fontSize:subtitleFontSize(legend.subtitle)}">{{legend.subtitle}}</p>
                    <!--used when multiple titles are requested on the same line-->
                    <div v-if="legend.type == 'pointSizeColor' || legend.type == 'polyLine' || legend.type == 'polyGrid'">
                        <h2 v-for="(item, index) in legend.title" style="display:inline;" v-bind:key="item.index" v-bind:style="[index === 0 ? {'margin-left': '0px'} : {marginLeft: legend.titleMargin + 'px'}]">{{item}}</h2>
                    </div>
                    <!--title for selected features-->
                    <h3 v-bind:id="['selectionTitle-' + initial]" v-if="legend.type == 'selection'" style="margin-top:10px;" class="selectionTitle">{{legend.title}}</h3>
                    <h3 v-bind:id="['selectionTitle-' + initial]" v-if="legend.type == 'selectionChart' || legend.type == 'selectionChartOnly'" class="selectionTitle">{{legend.title}}</h3>
                    <h2 id="hoverContainer" v-if="legend.hoverContainer == true" v-bind:style="[legend.type === 'point' || legend.type === 'pointRow' || legend.type === 'doubleLine' || legend.type === 'line' ? {'margin-bottom': '5px'} : {'margin-bottom': '10px'}]"></h2>
                    <!--chart contained if chart appears in legend and changes on hover-->
                    <div v-if="legend.type == 'chart'" v-bind:style="legend.chartHeight != null ? {'height':legend.chartHeight + 'px'} : {'height':'auto'}">
                        <canvas v-if="legend.type == 'chart'" class="chart" v-bind:height="legend.chartHeight != null ? legend.chartHeight : 160" v-bind:id="legend.chartID" style="width:290px; margin-left:-10px;"></canvas>
                    </div>
                    <!--legend that appears when a feature is selected-->
                    <div class="selectionContainer" v-if="legend.type == 'selection' || legend.type == 'selectionChart'" style="display:none;" >
                        <div v-if="legendData.jsonLayers[0].selection != null">
                            <h3 v-if="legendData.jsonLayers[0].selection.subtitleVar != null" v-bind:style="legendData.jsonLayers[0].selection.subtitleType != null ? [{'font-weight': 'normal'},{'font-style': 'italic'},{'margin-bottom': '10px'}] : [{'font-weight': 'bold'}]" id="selectionSubtitle"></h3>
                            <p v-if="legend.secondTitle" class="title">{{legend.secondTitle}}</p>
                            <p v-if="legend.subtitle" class="subtitle">{{legend.subtitle}}</p>
                            <div v-if="legend.varList == null || legend.varList == true">
                                <img id="selectionImage" v-if="legendData.jsonLayers[0].selection.imageLink" width="280">
                                <div v-for="(item, index) in legendData.jsonLayers[0].selection.varLabel" v-bind:key="item.index" style="margin-bottom:5px;">
                                    <p v-bind:style="legend.listWeight != null ? [{'font-weight': 'normal'},{'line-height': '26px'}] : [{'font-weight': 'bold'}], legend.varListFont != null ? [{'font-size': legend.varListFont + 'px', 'line-height': legend.varListFont + 4 + 'px'}] : [{'font-size': '20px'}]" class="varList" v-bind:id="'varLabel-' + index">{{item}}</p>
                                    <p v-bind:style="legend.listWeight != null ? [{'font-weight': 'normal'},{'line-height': '26px'}] : [{'font-weight': 'bold'}], legend.varListFont != null ? [{'font-size': legend.varListFont + 'px', 'line-height': legend.varListFont + 4 + 'px'}] : [{'font-size': '20px'}]" class="varList" v-bind:id="'varContent-' + index"></p>
                                    <p style="display:inline; margin-left:0;"><sup v-if="legendData.jsonLayers[0].selection.legTooltip" class="tooltip" v-bind:id="'leg-' + legend.id + 'tooltip-' + index" @mouseenter="hoverTooltip(true, legendData.jsonLayers[0].selection.legTooltip[index], index, legend.id)" @mouseleave="hoverTooltip(false, legendData.jsonLayers[0].selection.legTooltip[index], index, legend.id)" style="top:-0.8em;">?</sup></p>
                                </div>
                            </div>
                            <canvas v-if="legend.type == 'selectionChart'" class="chart" v-bind:id="legend.chartID" v-bind:height="legend.chartHeight != null ? legend.chartHeight : 160" v-bind:style="legend.chartMargin != null ? {'margin-left':legend.chartMargin} : {'margin-left':'-10px'}" style="margin-top:10px; margin-bottom:10px;"></canvas>
                            <div v-if="legendData.jsonLayers[0].selection.chartDescription != null">
                                <h3 style="line-height:20px" v-for="item in legendData.jsonLayers[0].selection.chartDescription" v-bind:key="item.index" id="selectionChartDesc">{{item}}<br/></h3>
                            </div>
                            <h3 v-if="legendData.jsonLayers[0].selection.chartDescriptionItalic != null" id="selectionChartDescItalic">{{legendData.jsonLayers[0].selection.chartDescriptionItalic}}</h3>
                        </div>
                    </div>
                    <!--legend that only includes a chart upon layer selection-->
                    <div class="selectionContainer" v-if="legend.type == 'selectionChartOnly'" style="display:none;">
                        <h2 v-if="legend.secondTitle" class="secondTitle">{{legend.secondTitle}}</h2>
                        <canvas class="chart" v-bind:id="legend.chartID" v-bind:height="legend.chartHeight != null ? legend.chartHeight : 160" v-bind:style="legend.chartMargin != null ? {'margin-left':legend.chartMargin} : {'margin-left':'-10px'}" style="margin-top:20px; margin-bottom:10px;"></canvas>
                    </div>
                    <!--legend with polygon bins-->
                    <div class="polyContainer" v-bind:id="'legendContainer-'+index" v-if="legend.type == 'polygon'">
                        <div class='polygon' v-for="(item, index) in legend.containers" v-bind:key="item.index">
                            <b class="block" v-bind:style="{background: item.color}" ></b>
                            <p>{{item.label}}<sup v-if="item.tooltip" class="tooltip" v-bind:id="'leg-' + legend.id + 'tooltip-' + index" @mouseenter="hoverTooltip(true, item.tooltip, index, legend.id)" @mouseleave="hoverTooltip(false, item.tooltip, index, legend.id)">?</sup></p>
                        </div>
                        <img class="icon" id="legendIcon" v-if="legend.icon" v-bind:src="getURL(legend.icon)" v-bind:style="{top:iconTop(index)}">
                    </div>
                    <!--legend with two columns of points, one based on size, the other based on color-->
                    <div v-if="legend.type == 'pointSizeColor'" style="margin-top:5px;">
                        <div style="display:inline-block;">
                            <div class='point' v-for="item in legend.containers" v-bind:key="item.index">
                                <p class="pointLabel">{{item.sizeLabel}}</p>
                                <b class="circle" v-bind:style="{width: item.size + 'px', height:item.size + 'px', borderColor:item.borderColorSize != null ? item.borderColorSize: '#000000',borderWidth:item.sizeBorder != null ? item.sizeBorder + 'px': '0.5px',boxShadow:item.shadow != null ? '1px 1px 2px black': 'none'}"></b>
                            </div>
                        </div>
                        <div style="display:inline-block; margin-left:120px;">
                            <div class='point' v-for="item in legend.containers" v-bind:key="item.index">
                                <p class="colorLabel">{{item.colorLabel}}</p>
                                <b class="circle" v-bind:style="{background: item.color, borderWidth:item.border != null ? item.border + 'px': '0px', borderColor:item.borderColor != null ? item.borderColor: null, boxShadow:item.shadow != null ? '1px 1px 2px black': 'none'}" style="width:20px; height:20px; border:none; border-style:solid;"></b>
                            </div>
                        </div>
                    </div>
                    <!--legend with vertical point bins-->
                    <div v-if="legend.type == 'point'" v-bind:id="'legendContainer-'+index" style="display:inline-block;">
                        <div class='point' v-for="(item,index) in legend.containers" v-bind:key="item.index">
                            <p class="colorLabel" v-bind:style="{ marginLeft:item.margin + 'px'}">{{item.label}}</p>
                            <b class="circle" v-bind:id="'circle-' + index" v-bind:style="{background: item.background, width: item.size + 'px', height:item.size + 'px', marginTop:pointHeight(item.size),borderColor:item.color, borderStyle:'solid', borderWidth:item.border != null ? item.border + 'px': '2px', boxShadow:item.shadow != null ? '1px 1px 2px black': 'none'}" style="width:20px; height:20px;margin-left:0;"></b>
                        </div>
                        <img class="icon" id="legendIcon" v-if="legend.icon" v-bind:src="getURL(legend.icon)" v-bind:style="{top:iconTop(index)}">
                    </div>
                    <!--legend with horizontal polygon bins-->
                    <div v-if="legend.type == 'pointRow'">
                        <div class='point' v-for="item in legend.containers" v-bind:key="item.index" style="display:inline;">
                            <b class="circle" style="margin-bottom:12px;" v-bind:style="{width: item.size + 'px', height:item.size + 'px', background:item.background, borderColor:item.color, borderStyle:'solid', borderWidth:item.border != null ? item.border + 'px': '1px', boxShadow:item.shadow != null ? '1px 1px 2px black': 'none'}"></b>
                            <p class="sizeRow" v-bind:style="{fontSize:item.fontSize != null ? item.fontSize + 'px' : '20px'}">{{item.label}}</p>
                        </div>
                    </div>
                    <!--legend with line bins-->
                    <div v-if="legend.type == 'line'" >
                        <div class='line' v-for="(item,index) in legend.containers" v-bind:key="item.index" v-bind:style= "[index == (item.length - 1) ? {'margin-bottom':'0px'} : {'margin-bottom':'10px'}]">
                            <hr v-bind:style="{height: item.height + 'px', background:item.color, marginTop:lineHeight(item.height)}" style="width:35px; line-height:20px; vertical-align:middle; display:inline; border:none; float:left;">
                            <p class="lineLabel">{{item.label}}</p>
                            <br/>
                        </div>
                    </div>
                    <!--legend with dashed line bins-->
                    <div v-if="legend.type == 'dashedLine'" >
                        <div class='line' v-for="(item,index) in legend.containers" v-bind:key="item.index" v-bind:style= "[index == (item.length - 1) ? {'margin-bottom':'0px'} : {'margin-bottom':'10px'}]">
                            <hr style="width:35px; line-height:20px; vertical-align:middle; display:inline; float:left; border: 1px dashed #a6a6a6; margin-top:15px;">
                            <p class="lineLabel">{{item.label}}</p>
                            <br/>
                        </div>
                    </div>
                    <!--legend with two columns of both polygons and lines-->
                    <div v-if="legend.type == 'polyLine'" class="polyContainer" style="margin-top:5px;">
                        <div v-for="item in legend.containers" v-bind:key="item.index" class='polygon'>
                            <b class="block" v-bind:style="{background: item.color}" ></b>
                            <p style="margin-left:30px;">{{item.label}}</p>
                            <hr v-bind:style="{height: item.height + 'px', background:item.color2, marginTop:lineHeight(item.height), borderTop:item.borderTop}" style="margin-left: 150px; width:35px; line-height:20px; vertical-align:middle; display:inline; border:none; float:left; position:absolute;">
                        </div>
                    </div>
                    <!--legend with two line bins-->
                    <div v-if="legend.type == 'doubleLine'" >
                        <div class='line' v-for="(item,index) in legend.containers" v-bind:key="item.index" v-bind:style= "[index == (item.length - 1) ? {'margin-bottom':'0px'} : {'margin-bottom':'10px'}]">
                            <div>
                                <hr v-bind:style="{height: item.height + 'px', background:item.color, marginTop:lineHeight(item.height)}" style="width:35px; line-height:20px; vertical-align:middle; display:inline; border:none; float:left;">
                                <p class="lineLabel">{{item.label}}</p>
                            </div>
                            <div style="margin-left:150px;">
                                <hr v-bind:style="{height: item.height2 + 'px', background:item.color2, marginTop:lineHeight(item.height2)}" style="width:35px; line-height:20px; vertical-align:middle; display:inline; border:none; float:left;">
                                <p class="lineLabel">{{item.label2}}</p>
                            </div>
                            <br/>
                        </div>
                    </div>
                    <!--legend with a gradient bar-->
                    <div v-if="legend.type == 'gradient'">
                        <b class="gradient" v-bind:style="{background: legend.gradient}"></b>
                        <div v-if="legend.gradientLabels != null">
                            <p class="gradientLabel" v-for="(item, index) in legend.gradientLabels" v-bind:key="item.index" v-bind:style="[index === 0 ? {'margin-left': '0px'} : {marginLeft: legend.gradientMargin + 'px'}]">{{item}}</p>
                        </div>
                    </div>
                    <!--legend with a 4x4 grid of polygon bins-->
                    <div v-if="legend.type == 'polyGrid'" class="polyGrid" style="margin-top:5px;">
                        <div v-for="item in legend.containers" v-bind:key="item.index" style="height:30px;">
                            <b class="polyGradient" v-bind:style="{background: item.gradient}"></b>
                            <p class="polyGridLabel">{{item.label}}</p>
                        </div>
                    </div>
                    <!--legend with narrow gradient bar-->
                    <div v-if="legend.type == 'narrowGradient'">
                        <b class="narrowGradient" v-bind:style="{background: legend.gradient}"></b>
                    </div>
                    <!--legend with icon bins-->
                    <div v-if="legend.type == 'icon'" v-bind:id="'legendContainer-'+index">
                        <div v-for="item in legend.containers" v-bind:key="item.index">
                            <img id="icon" v-bind:src="getURL(item.icon)" v-bind:height="item.size" v-bind:width="item.size">
                            <p class="colorLabel" v-bind:style="{ marginLeft:item.margin + 'px'}">{{item.label}}</p>
                        </div>
                        <img class="icon" id="legendIcon" v-if="legend.icon" v-bind:src="getURL(legend.icon)" v-bind:style="{top:iconTop(index)}">
                    </div>
                    <!--legend with two columns of both polygon and icon bins-->
                    <div v-if="legend.type == 'iconPoly'" class="polyContainer" v-bind:id="'legendContainer-'+index">
                        <div class='polygon' v-for="(item, index) in legend.containers" v-bind:key="item.index">
                            <b class="block" v-bind:style="{background: item.color}" ></b>
                            <img id="icon" v-bind:src="getURL(item.icon)" v-bind:height="item.size" v-bind:width="item.size" v-bind:style="{ marginLeft:item.iconMargin + 'px'}">
                            <p v-bind:style="{ marginLeft:item.margin + 'px'}">{{item.label}}<sup v-if="item.tooltip" class="tooltip" v-bind:id="'tooltip-' + index" @mouseenter="hoverTooltip(true, item.tooltip, index)" @mouseleave="hoverTooltip(false, item.tooltip, index)">?</sup></p>
                        </div>
                    </div>
                    <!--subtitle for a polygon grid-->
                    <div v-if="legend.subtitle != null && legend.type == 'polyGrid'">
                        <p class="polyGridBottom" v-for="item in legend.subtitle" v-bind:key="item.index" v-bind:style="{marginRight:legend.bottomMargin + 'px'}">{{item}}</p>
                    </div>
                    <!--text list legend-->
                    <div v-if="legend.type == 'list'">
                        <div v-for="item in legend.containers" v-bind:key="item.index" style="margin-bottom:10px;">
                            <p class="list" style="font-weight:bold;">{{item.labelTitle}}</p>
                            <p class="list" style="font-weight:normal;">{{item.label}}</p>
                        </div>
                    </div>
                    <!--legend for border layer-->
                    <div v-if="borders == true && legend.type == 'borders'">
                        <div class='line' v-for="(item,index) in legend.containers" v-bind:key="item.index" v-bind:style= "[index == (item.length - 1) ? {'margin-bottom':'0px'} : {'margin-bottom':'10px'}]">
                            <hr style="width:35px; line-height:20px; vertical-align:middle; display:inline; float:left; border: 1px dashed #a6a6a6; margin-top:15px;">
                            <p class="lineLabel">{{item.label}}</p>
                            <br/>
                        </div>
                    </div>
                    <!--description below legendd-->
                    <p class="description" v-if="legend.description" v-bind:style= "[legend.type == 'pointRow' ? {'margin-top':'0px'} : legend.type == 'heat' || legend.type =='gradient' ? {'margin-top':'5px'} : legend.type == 'chart' ? {'margin-top':'10px'} : legend.containers == null && legend.type == null ? {'margin-top':'0px'} : {'margin-top':'15px'}]">{{legend.description}} </p>
                    <!--second description level-->
                    <p class="subDescription" v-if="legend.subDescription">{{legend.subDescription}} </p>
                    <!--icon below the legend-->
                    <img class="bottomIcon" v-if="legend.bottomIcon" v-bind:src="getURL(legend.bottomIcon)" v-bind:style="[legend.bottomIconWidth != null ? {'width':legend.bottomIconWidth + 'px', 'margin-left':bottomIconMargin(legend.bottomIconWidth)} : {'width':'250px','margin-left':bottomIconMargin(legend.bottomIconWidth)}]">
                    <!--layer toggle, used primarily in the 'Fish' map-->
                    <ToggleSwitch v-if="legend.toggleSwitch" v-bind:toggleData="legend.toggleSwitch" @toggled="toggled" v-intro-step="5" v-intro="'Use to switch between layers.'"/>
                    <hr v-if="legend.bottomLine != null">
                </div>
            </div>
        </div>
        <div v-if="isChecked == true">
            <b class="block" id="checkLegend" v-bind:style="{background: checkLayer.legend.color}" ></b>
            <p>{{checkLayer.legend.label}}</p>
        </div>
        <!--quick facts elements-->
        <div v-if="legendData.fastFacts != null" style="padding-top:5px;">
            <div v-for="item in legendData.fastFacts" v-bind:key="item.index">
                <p id="facts" style="font-weight:bold; padding-bottom:10px; display:block;" v-if="item.fastTitle == null">Quick Facts:</p>
                <p id="facts" style="font-weight:bold; padding-bottom:10px; display:block;" v-bind:style="[item.titleColor != null ? {'color': item.titleColor} : {'color': '#000000'}]" v-else-if="item.fastTitle != 'none'">{{item.fastTitle}}</p>
                <p id="facts" style="margin-bottom:15px; display:inline-block;" v-for="(fact, index) in item.facts" v-bind:key="fact.index" ><b v-if="item.factHeader">{{decodeHtml(item.factHeader[index])}}</b>{{decodeHtml(fact)}}<br/></p>
                <br/>
            </div>
        </div>
   </div>
</template>

<script>

import ToggleSwitch from './ToggleSwitch.vue'

export default {
  name:'LegendContainer',
  components:{
    ToggleSwitch,
  },
  props:{
      legendData:Object,
      chapter:String,
      secondSelection:Number,
      checkLayer:Object,
      isChecked:Boolean,
      borders:Boolean,
      initial:Number,
      spyglassID:Number,
      spyglass:Boolean,
      spyglassLegend:Object,
      sliderValue:Number
  },
  mounted(){
      for (var i = 0; i < this.legendData.legend.length; i++){
          if (this.legendData.legend[i].icon)
            $("#legendIcon").css("top",this.iconTop(i)); 
      }
  },
  updated(){
      for (var i = 0; i < this.legendData.legend.length; i++){
          if (this.legendData.legend[i].icon)
            $("#legendIcon").css("top",this.iconTop(i));
          if (document.getElementById("chartTitle"))
            $("#chartTitle").html(this.legendData.legend[i].title);

          if (this.legendData.legend[i].type == 'selection' || this.legendData.legend[i].type == 'selectionChart'){
            var titleId = 'selectionTitle-' + this.initial;
            if (document.getElementById(titleId))
                $("#" + titleId).html(this.legendData.legend[i].title);
          }
      }
  },
  methods:{
    getURL(img){
        var images = require.context('../../assets/img/', true, /\.png$/)
        return images('./' + this.chapter + "/" + img + ".png")
    },
    pointHeight(height){
        var value = (28 - height) /2;
        return value + 'px';
    },
    lineHeight(height){
        var value = (32 - height) /2;
        return value + 'px';
    },
    iconTop(index){
        var containerId = "#legendContainer-"+index;
        var titleId = "#title-"+index;
        var subtitleId = "#mainSubtitle-"+index;
        var sub = document.getElementById("mainSubtitle-"+index) ? $(subtitleId).height() : 0;
        var value =  $(containerId).height() + $(titleId).height() + sub;
        var top = ((value)/2) - 35;
        return top + 'px';
    },
    bottomIconMargin(value){
        var margin = (280 - value)/2;
        return margin + 'px';
    },
    decodeHtml(html){
        var txt = document.createElement("textarea");
        txt.innerHTML = html;
        return txt.value;
    },
    subtitleFontSize(text){
          var length = text.length;
          if (length > 18)
            return '16px'
          else
            return '18px';
    },
    hoverTooltip(hover, text, index, leg){
        var id = "#leg-" + leg + "tooltip-" + index;
        var position = document.querySelector(id).getBoundingClientRect();
        this.$emit('tooltipHover', hover, text, position);
    },
    toggled: function(value){
        this.$emit('toggled', value);
    }
  },
  watch:{

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    div.polyContainer:before{
        content: "";
        display: block; 
