<template>
    <div id="pageChange">
        <button @click="openSource = true; openLink = false; openShare = false;" id="arrow" title="Map Sources/Info" v-intro-step="6" v-intro="'This button can be clicked to view map sources.'">
            <i class="fa fa-info-circle" aria-hidden="true"></i>
        </button>
        <button @click="openLink = true; openSource = false; openShare = false;" id="arrow" title="Other Maps" v-intro-step="7" v-intro="'This button can be clicked to select other maps in this chapter.'">
            <i class="fa fa-home" aria-hidden="true"></i>
        </button>
        <button @click="openShare = true; openLink = false; openSource = false;" id="arrow" title="Share Map" v-intro-step="8" v-intro="'This button can be clicked to share this map on social media.'">
            <i class="fa fa-share-alt " aria-hidden="true"></i>
        </button>
        <button id="arrow" title="Map Demo"  href="javascript:void(0);" v-on:click="intro()">
            <i class="fa fa-question-circle " aria-hidden="true"></i>
        </button>
        <SourcePopup v-bind:sources="sources" v-if="openSource == true" @close="closePopup()"/>
        <LinkPopup v-bind:chapterMaps="chapterMaps" v-if="openLink == true" @close="closePopup()"/>
        <SharePopup v-bind:share="sources" v-if="openShare == true" @close="closePopup()"/>
    </div>
</template>

<script>
import 'intro.js'

import SourcePopup from './SourcePopup.vue'
import LinkPopup from './LinkPopup.vue'
import SharePopup from './SharePopup.vue'

export default {
  name: 'BottomRow',
  components:{
      SourcePopup,
      LinkPopup,
      SharePopup
  },
  data() {
    return {
            openLink:false,
            openSource:false,
            openShare:false
        }
    },
  props:{
      sources:Object,
      chapterMaps:Object
  },
  mounted(){
        let self = this;
        window.addEventListener('click', function(event){
            if (!$(event.target).closest("#share, #sources, #links, #pageChange").length) {
                self.closePopup(false);
            }
        });
    },
  methods:{
      closePopup(value){
          this.openLink = false;
          this.openSource = false;
          this.openShare = false;
      },
      intro(){
          this.$intro().setOption('showBullets', false).setOption('showStepNumbers', false).setOption('tooltipClass', 'customDefault').start();
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div#pageChange{
	height:40px;
	width:300px;
	margin-right:20px;
	position:absolute;
	bottom:45px;
	left:82px;
}
button#arrow{
	color:black;
	font-size:28px;
    position:relative;
    display:inline-block;
    background:none;
    border:none;
} 
#arrow:hover{
	color:white;
    cursor:pointer;
}
</style>
