<template>
   <div class="container" id="container" v-bind:style="{lineHeight: height + 'px' ,top: top}">
       <div id=slider v-bind:style="[slider.height != null ? {height: slider.height + 'px'} : {'height': '360px'}]"></div>
       <div class="line" v-for="item in values" v-bind:key="item.key" v-bind:style="{height: height + 'px'}">
            <p v-if="item != selected" class="dash" v-bind:id="slider.title + item + '-dash'">-</p>
            <p v-else-if="item == selected" class="dash" v-bind:id="slider.title + item + '-dash'">—</p>
            <p v-if="item != selected" v-on:click="sliderChange(item)" v-bind:id="slider.title + item" class="value">{{item}}{{slider.suffix}}</p>
            <p v-else v-on:click="sliderChange(item)" v-bind:id="slider.title + item" class="value" style="font-weight:bold;">{{item}}{{slider.suffix}}</p>
        </div>
   </div>
</template>

<script>

export default {
  name:'TimlineSlider',
  props:{
      slider:Object,
  },
  data() {
    return {  
        values:null,
        selected:null,
        height:null,
        top:null
    }
  },
  mounted(){
      this.calcValues();
      this.sliderTop();
      window.addEventListener('resize', this.sliderTop);
  },
  methods:{
    calcValues(){
        var count = 0;
        if (this.slider.type == 'string'){
            this.values = this.slider.values;
            
            var sliderHeight = this.slider.height != null ? this.slider.height : 360;
            this.height = sliderHeight / this.slider.values.length;

            this.selected = this.slider.default;
            this.$emit('sliderChange', this.selected);
        }
        else{
            this.values = [];
            var range = (this.slider.max - this.slider.min + this.slider.step)/this.slider.step;

            for (var i = 0; i < range; i++){
                this.values[i] = this.slider.max - count;
                count += this.slider.step;
            }
            
            var sliderHeight = this.slider.height != null ? this.slider.height : 360;
            this.height = sliderHeight / range;

            this.selected = this.slider.default;
            this.$emit('sliderChange', this.selected);
        }
    },
    sliderChange: function(value){
        this.selected = value;
        this.$emit('sliderChange', value);
    },
    sliderTop(){
        var sliderHeight = this.slider.height != null ? (this.slider.height/2) : 180;
        var doc = $(window).height()/2;  
        this.top =  (doc - sliderHeight)  + 'px';
    }
  },
  watch:{
        slider: function(){
           this.calcValues(); 
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
   div.container{
        height:360px; 
        width:110px; 
        position:absolute; 
        left:320px; 
        z-index:2000; 
    }
    div#slider{
        width:10px; 
        position:absolute; 
        z-index:2100; 
        border-radius: 0px 3px 3px 0px;
        background:rgba(255,255,255,0.6);
    }
    p.value{
        position:relative;
        font-size:18px;
        margin-top:0;
        margin-bottom:0;
        margin-left:10px;
        display:inline;
        width:50px;
        /*border-left:2px solid white;*/
    }
    p.value:hover{
        font-weight:bold;
        cursor:pointer;
    }
    p.dash{
        color:white;
        display:inline;
        font-family:times;
        font-weight:bold;
        margin-left:10px;
        font-size:20px;
        font-smooth: never;
        -webkit-font-smoothing : none;
    }
</style>
