<template>
   <div id="top">
       <h1>{{title}}</h1>
       <img class="top" v-bind:src="getURL(URL)"/>
   </div>
</template>

<script>

export default {
  name:'TopTitle',
  props:{
      title:String,
      URL:String,
      chapter:String
  },
  methods:{
    getURL(img){
        var images = require.context("../../assets/img/", true, /\.png$/)
        return images('./' + this.chapter + "/" + img + ".png")
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
img.top{
    width: 60px;    
    height: 60px;
    left:110px;
    position:relative;
    bottom:15px;
    display:inline-block; 
}
h1{
    font-family:GoudyOld;
    font-size:42px;
    margin-top:15px;
    letter-spacing: 2px;
    width:280px;
    text-align:center;
    font-variant: small-caps;
}

</style>

