<template>
   <div class="tooltipContainer" id="divTooltip"><p id="tooltipText">{{tooltip}}</p></div>
</template>

<script>

export default {
  name:'InfoPanel',
  props:{
      tooltip:String,
      position:DOMRect
  },
  mounted(){
      this.setStyle();
  },
  updated(){
      this.setStyle();
  },
  methods:{
      setStyle(){
        $("#divTooltip").css('left',this.position.left - 100);
        $("#divTooltip").height($("#tooltipText").height());
        $("#divTooltip").css('top',this.position.top - $("#tooltipText").height() - 15);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tooltipContainer {
    width: 200px;
    overflow:normal;
    display:block;
    white-space:normal;
    background-color: #ffffff;
    box-shadow: 2px 2px 1px #595959;
    color: #000;
    text-align: left;
    padding: 5px;
    border-radius: 6px;
    border: black 2px solid;
    bottom: 85%;
    position: absolute;
    z-index: 1500;
    font-weight:none;
    font-size:14px;
    z-index:2000;
    line-height:18px;
    /* Position the tooltip text - see examples below! */
}
p#tooltipText{
    margin:0;
}
</style>
