<template>
  <img v-bind:src="getURL(image.URL)"/>
</template>

<script>
export default {
  name: 'ImageBanner',
  props: {
      image:Object
    },
  methods:{
    getURL(img){
        var images = require.context('../assets/img/', false, /\.png$/)
        return images('./' + img + ".png")
    }
  }      
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    img{
        width:250px;
        display:block;
        margin-bottom:20px;
    }
</style>
