<template>
    <div id="sources" class="overlay">
        <div class="popup">
            <h2 style="display:inline; font-size:32px;">Sources</h2>
            <button class="close" @click="close()" style="display:inline;">×</button>
            <hr>
            <div class="content">
                <div v-if="sources.contentNote" style="padding-bottom:15px;">
                    <p style="font-size:20px;">Content Notes</p><br><br/>
                    <p>{{sources.contentNote}}</p>
                </div>
                <p style="font-size:20px;">Geographic Data</p><br><br/>
                <div class="source" v-for="item in sources.geographic" v-bind:key="item.index">
                    <b v-if="item.title">{{item.title}} <a v-bind:href="item.linkUrl" target="_blank">{{item.linkText}}</a></b><br/>
                    <p v-if="item.description != null">{{item.description}}</p>
                    <a style="display:inline;" v-for="(linkText,index) in item.descriptionLinkText" v-bind:key="linkText.index" v-bind:href="item.descriptionLinkUrl[index]" target="_blank">{{decodeHtml(item.descriptionLinkText[index])}}</a>
                </div>
                <p v-if="sources.text" style="font-size:20px;">Other Data<br><br/></p>
                <div v-if="sources.text" class="source">
                    <div v-for="item in sources.text" v-bind:key="item.index">
                        <b>{{item.title}} <a v-bind:href="item.linkUrl" target="_blank">{{item.linkText}}</a></b><br/>
                        <p v-if="item.description != null">{{item.description}}</p>
                        <a style="display:inline;" v-for="(linkText,index) in item.descriptionLinkText" v-bind:key="linkText.index" v-bind:href="item.descriptionLinkUrl[index]" target="_blank">{{decodeHtml(item.descriptionLinkText[index])}}</a>
                    </div>
                </div>
                <div v-if="sources.geographyNote" style="padding-bottom:15px;" class="source">
                    <p style="font-size:20px;">Geography Note</p><br><br/>
                    <p>{{sources.geographyNote}}</p><b><a v-bind:href="sources.geographyNoteLink" target="_blank">{{sources.geographyNoteLinkTitle}}</a></b>
                </div>
                <p style="font-size:20px;">Basemap</p><br><br/>
                <div class="source" v-for="item in sources.basemap" v-bind:key="item.index">
                    <b>{{item.title}} <a v-bind:href="item.linkUrl" target="_blank">{{item.linkText}}</a></b><br/>
                        <p v-if="item.description != null">{{item.description}}</p>
                        <a style="display:inline;" v-for="(linkText,index) in item.descriptionLinkText" v-bind:key="linkText.index" v-bind:href="item.descriptionLinkUrl[index]" target="_blank">{{decodeHtml(item.descriptionLinkText[index])}}</a>
                </div>
            </div>
            <hr>
            <div style="display:inline; float:right;" v-for="(item,index) in sources.softwareText" v-bind:key="item.index" v-bind:style="[index === 0 ? {'margin-left': '0px'} : {'margin-left': '0px'}]" id="map_creation">
                <p v-if="index == (sources.softwareText.length - 1)">Map created with </p>
                <p v-if="index == 0">and </p>
                <a v-bind:href="sources.softwareUrls[index]" target="_blank">{{decodeHtml(item)}}</a>
                <p v-if="index != 0">,&nbsp</p>
            </div>
            <br/>
            <button class="close2" @click="close()" style="display:inline;">Close</button>
        </div>
	</div>
</template>

<script>
export default {
  name: 'SourcePopup',
  props:{
      sources:Object
  },
  mounted(){
      this.resizePopup();
      window.addEventListener('resize', this.resizePopup);
  },
  methods:{
      decodeHtml(html){
        var txt = document.createElement("textarea");
        txt.innerHTML = html;
        return txt.value;
      },
      resizePopup(){
        var h = $('.popup').height() - 90;
        h = h > 50 ? h : 50;
        $('.popup .content').height(h);
      },
      close(){
         this.$emit('close', false);
      }
  }
}
</script>
    
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity 500ms;
    visibility: visible;
    opacity: 1;
    z-index:10000;
}

.popup {
    margin: 70px auto;
    padding: 20px;
    padding-bottom: 50px;
    background: #fff;
    border-radius: 5px;
    width: 45%;
    position: relative;
    transition: all 5s ease-in-out;
    font-family:GoudyOld;
    max-height:50%;  
}

.popup h2 {
    margin-top: 0;
    color: #333;
    font-weight:normal;
}

.popup p {
    margin-top: 0;
    margin-bottom:0;
    display:inline;
}

.popup #mapCreation p {
    display:inline;
    font-size:16px;
}

hr{
    border:1px solid #e6e6e6;
    border-bottom-width:0px;
}

.popup .close {
    top:15px;
    right:20px;
    position: absolute;
    transition: all 200ms;
    font-size: 20px;
    text-decoration: none;
    color: #d9d9d9;
    text-align:right;
    font-family:GoudyOld;
    background:none;
    border:none;
}

.popup .close:hover {
    color: #b3b3b3;
    cursor:pointer;
}

.popup .close2{
    margin-top:5px;
    background-color: white; 
    transition: all 200ms;
    border: 1px solid #d9d9d9;
    border-radius:5px;
    color: black;
    padding: 5px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 14px;
    font-family:GoudyOld;
    float:right;
}
    
.popup .close2:hover{
    background-color: #f2f2f2; 
    cursor:pointer;
}
.popup .content{
    overflow: auto;
}
.popup div.source{
    padding-bottom:15px;
}	
.popup div.source a, .popup #map_creation a{
    color:rgb(52,123,181);
    text-decoration:none;
}
.popup div.source a:hover, .popup #map_creation a:hover{
    color:#004466;
    text-decoration:underline;
}	
@media screen and (max-width: 700px){
    .box{
        width: 70%;
    }
    .popup{
        width: 70%;
    }
}
</style>
