<template>
  <div class="chapter-box" v-on:mouseover="chapter.active = !chapter.active" v-on:mouseout="chapter.active = !chapter.active">
        <nav>
            <router-link v-bind:to="chapter.link">
                <img class="chapter-icon" v-bind:id="chapter.id" v-bind:src="getURL(chapter.imageURL)"/>
                <img class="backgroundImg" v-bind:style="[chapter.active ? {opacity: 1} : {opacity: 0}]" v-if="chapter.active" v-bind:src="getURL(chapter.backgroundURL)"/>
                <div class="map-text">
                    <h3 class="chapter-title">{{ chapter.text }}</h3>
                    <p class="chapter-desc">{{ chapter.description }}</p>
                </div>
            </router-link>
        </nav>
  </div>
</template>

<script>
export default {
  name: 'ChapterList',
  props: {
      chapter:Object
    },
  methods: {
        imgPosition(){
            var height =$("#chapter-box").height()/4;
            $("#"+this.chapter.id).css('margin-top', height);
        },
        getURL(img){
            var chapter = this.chapter.chapter != null ? this.chapter.chapter + "/" : "";
            var images = require.context('../assets/img/', true, /\.png$/);
            return images("./" + chapter + img + ".png");
        }
    },
  mounted() {
        $(window).resize(this.imgPosition);
    }      
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    div.chapter-box{
        position:relative;
        overflow:hidden;
        padding: 10px 0px 0px 10px;
        margin:auto;
    }

    div.chapter-box:hover{
        cursor: pointer;
    }

    div.map-text{
        margin-left:100px; 
        display:inline-block;
        position:relative;
    }

    h3.chapter-title{
        color: rgba(0,0,0,1);
        padding:0;
        margin:0;
        font-size: 30px;
        line-height: 30px;
        display:inline;
        text-align:center;
    }
    
    img.chapter-icon{
        height: 40px;
        width: 40px;
        position:absolute;
        top:35px;
        margin:auto;
        display:inline-block;
        text-align:left;
        z-index:1;
    }

    img.backgroundImg{
        position:absolute;
        left:0;
        top:0;
        z-index:0;
    }

    p.chapter-desc {
        font-size:18px;
        color:black;
        margin-top:0px;
        text-align:left;
        width:65%;
    }
</style>
