<template>
   <button id="collapse" v-on:click="collapse()">
	    <i class="fa fa-chevron-left" id ="collapse-arrow" aria-hidden="true"></i>
    </button>
</template>

<script>

const L = window.L;
export default {
  name:'CollapseInfo',
  data() {
    return {
        active:true
    }
 },
  props:{
      divID:String,
  },
  methods: {
        collapse(){
            if (this.active == true){
                document.getElementById(this.divID).style.display = "none";
    		    document.getElementById("collapse").style.left = '0px';
    		    document.getElementById("collapse").style.paddingLeft = '2px';
    		    document.getElementById("collapse-arrow").className = "fa fa-chevron-right";
                this.active = false;
            }
            else{
                document.getElementById(this.divID).style.display = "block";
                document.getElementById("collapse").style.left = '320px';
    		    document.getElementById("collapse").style.paddingLeft = '0px';
    		    document.getElementById("collapse-arrow").className = "fa fa-chevron-left";
                this.active = true;
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped> 
#collapse{
	position: absolute;
    left:320px;
	width:16px;
	height:30px;
	top:50%;
	font-family:GoudyOld;
	z-index:500;
	font-size:18px;
	padding:3px 4px 0px 0px;
	border: 0px solid #808080;
	margin:0;
	color:#000000;
	background:rgba(255,255,255,0.6);
	cursor:pointer;
}
#collapse:hover{
	background:rgba(255,255,255,1);
	border: 0px solid #595959;
	color:#cccccc;
}

</style>
