<template>
  <div id="chapter" v-if="chapter">
    <VuePerfectScrollbar class="scroll-area2" id="scroll-area2"  v-once :settings="settings">
        <TopNavbar type="chapter" title="Back" v-bind:links="[{title:'OSU Libraries & Press | Institute for Natural Resources', link:'/'}]"/>
        <ScrollArrowRight/>
        <!--HTML for the sources page-->
        <div v-if="chapter.chapterTitle == 'Sources'" id="chapterContent">
            <SectionTitle v-if="chapter.chapterTitle" v-bind:title="chapter.chapterTitle" id="chapters" />
            <p style="width:50%; font-size:18px;">This is a comprehensive list of data and software sources for the Atlas of the Pacific Northwest. Sources for individual maps can also be viewed on map pages by selecting the 'Info' button on the bottom right of each page.</p>
            <div class="sourceList" v-for="chapter in chapter.chapters" v-bind:key="chapter.index">
              <h2 style="font-size:36px; margin-bottom:5px;">{{chapter.title}}</h2>
              <div v-for="map in chapter.maps" v-bind:key="map.index">
                <h2 style="margin-top:15px;">{{map.title}}</h2>
                <div v-for="item in map.sources" v-bind:key="item.index">
                    <b v-if="item.title">{{item.title}} <a v-bind:href="item.linkUrl" target="_blank">{{item.linkText}}</a></b>
                    <div style="width:50%;"> 
                        <p v-if="item.description != null">{{item.description}}
                        <a class="decriptionLink" style="display:inline;" v-for="(linkText,index) in item.descriptionLinkText" v-bind:key="linkText.index" v-bind:href="item.descriptionLinkUrl[index]" target="_blank">{{decodeHtml(item.descriptionLinkText[index])}}</a>
                        </p>
                    </div>
                </div>
              </div>
            </div>
        </div>
        <!--HTML for chapter homepages-->
        <div v-else id="chapterContent">
          <SectionTitle v-bind:title="chapter.chapterTitle" id="chapters" />
          <div style="width:70%;">
              <ChapterList   
                  v-for="item in chapter.maps"
                  v-bind:chapter="item"
                  v-bind:key="item.id">
              </ChapterList>
          </div>
        </div>
    </VuePerfectScrollbar>
  </div>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import TopNavbar from '../components/TopNavbar.vue'
import ScrollArrowRight from '../components/ScrollArrowRight.vue'
import SectionTitle from '../components/SectionTitle.vue'
import ChapterList from '../components/ChapterList.vue'

//import chapterData from './page_data/food/food_home'

export default {
  name: 'home',
  components: {
    TopNavbar,
    ScrollArrowRight,
    SectionTitle,
    VuePerfectScrollbar,
    ChapterList,
  },
  data() {
    return {
      settings: {
        maxScrollbarLength: 1000,
        scrollXMarginOffset: 1000
        },
      chapter: null,
        }
    },
  created(){
      this.fetchData();
    },
  watch:{
      '$route':'fetchData'
  },
  methods:{
     fetchData(){
        let self = this;  
        
        var fileName = "./page_data/" + this.$route.params.chapter + "/" + this.$route.params.chapter + ".json";
        const myRequest = new Request(fileName);

        fetch(myRequest)
          .then( res => res.json())
          .then(data => {
              self.chapter = data;
              console.log('Request successful', data);
              document.title = self.chapter.chapterTitle;
              $('meta[property="og:title"]').remove();
              $('head').append( '<meta property="og:title" content="">');
              $("meta[property='og:title']").attr("content", "Atlas of the Pacific Northwest: " + self.chapter.chapterTitle);
          })
          .catch(function(error) {
            console.log('Request failed', error)
          });
     },
     decodeHtml(html){
        var txt = document.createElement("textarea");
        txt.innerHTML = html;
        return txt.value;
      }
  }
}

</script>

<style>
@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

div#chapter {
  font-family: GoudyOld;
  height:100%;
  left:0;
  width: 100%;
  z-index:1;
  background:url(../assets/img/pnw_boundary2.jpg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-position:70% 50%;
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;  
	-ms-animation: 1s ease-out 0s 1 slideInFromLeft;
	animation: 1s ease-out 0s 1 slideInFromLeft;
}
.scroll-area2{
  height:100%;
  position:relative;
  left:50%;
  width: 50%;
  z-index:1;
  bottom:45px;
}
#chapterContent{
  width:100%;
  margin:0 auto;
  padding:0;
  border-radius: 10px;
  padding-left:20%;
  padding-top:50px;
  left:20%;
}
.sourceList p{
    margin-top:5px;
    display:inline-block;
}
.sourceList b{
    width:50%;
    display:block;
}
.sourceList a{
    color:rgb(52,123,181);
    text-decoration:none;
}
.sourceList a.descriptionLink{
    color:rgb(52,123,181);
    text-decoration:underline;
}
.sourceList a:hover{
    color:#004466;
    text-decoration:underline;
}	
</style>
