<template>
    <div id="map" >
        <a v-if="basemapType == 'mapbox'" href="http://mapbox.com/about/maps" class='mapbox-wordmark' target="_blank">Mapbox</a>
        <!--Button used to toggle political borders on the map-->
        <button type="button" class='centerMap' v-on:click="borderToggle" id="borderToggle" value="selected" v-intro-step="12" v-intro="'Use to toggle political borders.'">Toggle Borders</button>
    	<!--Button used to center the map-->
        <button type="button" class='centerMap' v-on:click="centerMap" id="centerMap" v-intro-step="13" v-intro="'Select to center the map on the PNW.'">Center PNW</button>
    </div>
</template>

<script>

import 'leaflet'
import 'leaflet.heat'
import 'leaflet.pattern'
import 'topojson'
import 'papaparse'
import 'chart.js'
import 'chartjs-color-string'
import 'chartjs-color'
import 'chartjs-plugin-labels'
import 'overlapping-marker-spiderfier-leaflet'
import 'vue-plugin-load-script'
import 'jquery-csv'

const L = window.L;
export default {
  name:'BaseMap',
  data() {
    return {
        map:null,
        basemapLayer:new Array(),
        tiles:null,
        json:null,
        selectionJson:new Array(),
        info:null,
        chart:null,
        sliderValue:null,
        prevURL:null,
        flowSelection:null,
        flowCanvas:null,
        tooltipText:new Array(),
        tooltipVars:new Array(),
        tool:new Array(),
        sticky:new Array(),
        borders:false,
        borderLayer:null,
        url:null,
        sameUrl:false,
        oms:null,
        checkLayer:new Array(),
        spyglassLayer:null,
        activeLayer:new Array ()
    }
 },
  props:{
      chapter:String,
      basemap:Array,
      layers:Array,
      initial:Number,
      counter:Number,
      slide:Number,
      featureSelected:Boolean,
      zoom:Number,
      filterValue:String,
      bounds:Array,
      center:Array,
      secondDropdownButtons:Object,
      basemapType:String,
      check:Boolean,
      checkJson:Object,
      isToggled:Boolean,
      spyglass:Boolean,
      spyglassID:Number
  },
  mounted() {
    //script used for topoJSON files
    L.TopoJSON = L.GeoJSON.extend({
  		addData: function(jsonData) {
    		if (jsonData.type === 'Topology') {
      			for (this.key in jsonData.objects) {
       			    var geojson = topojson.feature(jsonData, jsonData.objects[this.key]);
       			    L.GeoJSON.prototype.addData.call(this, geojson);
      		    }
    	    }
    	    else {
      		        L.GeoJSON.prototype.addData.call(this, jsonData);
    		    }
  		    }
        });
    //script used to accept string data from a JSON file convert it to usable variables
    Object.byString = function(o, s) {
                s = s.replace(/\[(\w+)\]/g, '.$1'); // convert indexes to properties
                s = s.replace(/^\./, '');           // strip a leading dot
                var a = s.split('.');
                for (var i = 0, n = a.length; i < n; ++i) {
                    var k = a[i];
                    if (k in o) {
                        o = o[k];
                    } else {
                        return;
                    }
                }
                return o;
            }


    this.json = [];
    this.addScripts();
    this.initMap();
    this.initLayers();

    var self = this;

  },
  methods:{
      addScripts(){
        //add javascript (.js) files used for a heatmap
        for (var i = 0; i < this.layers.length; i++)
        {
            if (this.layers[i].jsonLayers){
                for (var j = 0; j < this.layers[i].jsonLayers.length; j++){
                    if (this.layers[i].jsonLayers[j].feature == 'heat'){
                        var callback = function(){
                            //console.log("script added");
                        }
                        let script = document.createElement('script');
                            script.setAttribute('src', this.layers[i].jsonLayers[j].jsUrl);
                            document.head.appendChild(script);
                            script.async = true;
                            script.onload = callback;
                    }
                }
            }
        }
        //add the flowmap script
        var flowmap = document.createElement('script');
            flowmap.type = 'text/javascript';
            flowmap.src = "js/CanvasFlowmapLayer.js";
                document.getElementsByTagName('head')[0].appendChild(flowmap);

      },
      //add images
      getURL(img){
        var images = require.context('../../assets/img/', true, /\.png$/)
        return images('./' + this.chapter + "/" + img + ".png")
      },
      //center map
      centerMap(){
            var width = document.documentElement.clientWidth;

            var lat = this.center != null ? this.center[0] : 45.70,
                lon = this.center != null ? this.center[1] : -119.75,
                zoom = this.zoom != null ? this.zoom : 6;

            if (width < 1500 && width > 800) {
                this.map.setView(new L.LatLng(lat, lon-2), zoom);
            }  else if (width > 1501) {
                this.map.setView(new L.LatLng(lat, lon), zoom+1);
            } else {
                this.map.setZoom(zoom-1);
            }
      },
      //create border layer
      borderCreation(){
            var borderType,
                borderPane,
                borderOpacity;

            if (this.layers[this.initial].border){
                borderType = this.layers[this.initial].border.title ? this.layers[this.initial].border.title : 'pnw_borders_black_dark';
                borderOpacity = this.layers[this.initial].border.opacity ? this.layers[this.initial].border.opacity : 0.4;
                borderPane = this.layers[this.initial].border.pane ? this.layers[this.initial].border.pane : 500;
            }
            else{
                borderType = 'pnw_borders_black_dark';
                borderOpacity = 0.4;
                borderPane = 500;
            }

            var borderLayer = L.tileLayer.wms('https://geoserver.library.oregonstate.edu/geoserver/PNW_ATLAS/wms', {
                layers: borderType,
                format: 'image/png',
                maxZoom:11,
                minZoom:5,
                opacity:borderOpacity,
                transparent:true,
                pane:"borderPane"
            });

            this.map.createPane('borderPane');
            this.map.getPane('borderPane').style.pointerEvents = 'none';
            this.map.getPane('borderPane').style.zIndex = borderPane;

            this.borderLayer = borderLayer;
            this.borderLayer.addTo(this.map);

            this.$emit('borders', true);
            this.borders = true;
      },
      //toggle border layer
      borderToggle(){
            if (this.borders == false){
                this.borderCreation();
                this.borders = true;
            }
            else{
                this.borderLayer.remove(this.map);
                this.$emit('borders', false);
                this.borders = false;
            }
      },
      //initialize map
      initMap(){
        var bounds = this.bounds != null ? L.latLngBounds(this.bounds[0],this.bounds[1]) : L.latLngBounds([21.172626315855425, -149.58519708215204], [66.10396253416393, -82.34035998361294]),
            zoom = this.zoom != null ? this.zoom : 6;

        //create map object
        this.map = L.map('map', {center: [45.60, -121.75], zoom: zoom, zoomControl:false,maxBounds:bounds});

        //create various map panes
        this.map.createPane('labels');
        this.map.getPane('labels').style.pointerEvents = 'none';
        this.map.getPane('labels').style.zIndex = 650;

        this.map.createPane('underFlow');
        this.map.getPane('underFlow').style.pointerEvents = 'auto';
        this.map.getPane('underFlow').style.zIndex = 300;

        this.map.createPane('overJson');
        this.map.getPane('overJson').style.pointerEvents = 'auto';
        this.map.getPane('overJson').style.zIndex = 450;

        this.map.createPane('overBorder');
        this.map.getPane('overBorder').style.pointerEvents = 'auto';
        this.map.getPane('overBorder').style.zIndex = 550;

        this.map.createPane('tooltip');
        this.map.getPane('tooltip').style.zIndex = 750;
        //initialize basemap layers
        if (this.basemap){
            for (var i = 0; i < this.basemap.length; i++){
                var link = this.basemap[i].title == "streets" ? 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_nolabels/{z}/{x}/{y}.png' :
                        this.basemap[i].title == "streetsDark" ? 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_nolabels/{z}/{x}/{y}.png' :
                        this.basemap[i].title == "imagery" ? 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}' :
                        this.basemap[i].title == "label" ? 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_only_labels/{z}/{x}/{y}@2x.png' :
                        this.basemap[i].title == "labelDark" ? 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/dark_only_labels/{z}/{x}/{y}@2x.png' :
                        this.basemap[i].title == "uslabel" ? 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/light_only_labels/{z}/{x}/{y}@2x.png' :
                        this.basemap[i].title == "terrain" ? 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Terrain_Base/MapServer/tile/{z}/{y}/{x}' :
                        this.basemap[i].title == "geoserver" ? 'https://geoserver.library.oregonstate.edu/geoserver/PNW_ATLAS/wms' :
                        this.basemap[i].title == "marineBackground" ? 'https://api.mapbox.com/styles/v1/pnwatlas/cjumyke6p17m01fmzjmncujhq/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoicG53YXRsYXMiLCJhIjoiY2pnM3puY3hwMXVldTJxcXBjZnZseG1jbCJ9.AXk0tP-pS2HjpgLJahLcdw' :
                        this.basemap[i].title == "marineLabel" ? 'https://api.mapbox.com/styles/v1/pnwatlas/cjv49vru51cat1fqcqcap1iqo/tiles/256/{z}/{x}/{y}@2x?access_token=pk.eyJ1IjoicG53YXRsYXMiLCJhIjoiY2pnM3puY3hwMXVldTJxcXBjZnZseG1jbCJ9.AXk0tP-pS2HjpgLJahLcdw' :
                            this.basemap[i].title;
                var pane = this.basemap[i].title == 'label' || this.basemap[i].title == 'uslabel' || this.basemap[i].title == 'labelDark' || this.basemap[i].pane == 'labels' || this.basemap[i].title == 'marineLabel' ? 'labels' : 'tilePane',
                    bounds = this.basemap[i].title == 'label' ? [new L.LatLng(49.56,-125.25), new L.LatLng(40.5,-111.5)] : null,
                    maxZoom = this.basemap[i].maxZoom != null ? this.basemap[i].maxZoom : 11,
                    minZoom = this.basemap[i].minZoom != null ? this.basemap[i].minZoom : 4;
                if (this.basemap[i].title == 'geoserver'){
                    this.basemapLayer[i] = L.tileLayer.wms('https://geoserver.library.oregonstate.edu/geoserver/PNW_ATLAS/wms', {
                        layers: this.basemap[i].layer,
                        format: 'image/png',
                        maxZoom:maxZoom,
                        minZoom:minZoom,
                        opacity:this.basemap[i].opacity,
                        transparent:true
                    });
                }
                else{
                    this.basemapLayer[i] = L.tileLayer(link, {
                        format: 'image/png',
                        maxZoom:maxZoom,
                        minZoom:minZoom,
                        opacity:this.basemap[i].opacity,
                        transparent:true,
                        pane:pane,
                        bounds:bounds
                    });
                }
                this.basemapLayer[i].addTo(this.map);
            }
        }

        var width = document.documentElement.clientWidth;

        this.centerMap();

        //create info control in the upper right corver of the map
        this.info = L.control();

        this.info.onAdd = function (map) {
            this._div = L.DomUtil.create('div', 'info'); // create a div with a class "info"
            return this._div;
        };

        new L.Control.Zoom({ position: 'bottomright' }).addTo(this.map);

        //remove attribution at the bottom of the page--attribution is included in the info panel
        document.getElementsByClassName("leaflet-control-attribution")[0].style.visibility = "hidden";
        //include attribution if one or more of the basemap layers is from mapbox
        if (this.basemapType == 'mapbox'){
            document.getElementsByClassName("leaflet-control-attribution")[0].innerHTML = "<a style='font-weight:bold' href='https://leafletjs.com/'>Leaflet</a> | © <a style='font-weight:normal' href='https://www.mapbox.com/about/maps/'>Mapbox</a> © <a style='font-weight:normal' href='http://www.openstreetmap.org/copyright'>OpenStreetMap</a> <strong><a href='https://www.mapbox.com/map-feedback/' target='_blank'>Improve this map</a></strong>";
            document.getElementsByClassName("leaflet-control-attribution")[0].style.visibility = "visible";
        }
      },
      //create thematic map layers
      initLayers(){
          //reset chart
          if (this.chart){
              for (var j = 0; j < this.chart.length; j++)
                this.chart[j].destroy();
          }
          this.chart = [];
          if (this.layers[this.initial].chart){
              if (this.layers[this.initial].chart[0].chartLoc == 'legend'){
                for (var i = 0; i < this.layers[this.initial].chart.length; i++){
                    this.initChart(this.layers[this.initial].legend[this.layers[this.initial].chart[i].id].chartID,this.layers[this.initial].chart[i],i);
                }
              }
          }
          //create a timeline slider if indicated in the JSON layer
          if (this.layers[this.initial].slider)
                this.sliderValue = this.layers[this.initial].slider.default;
          //add tile layers
          if (this.layers[this.initial].tileSlider)
            this.initTileLayers(this.layers[this.initial].tileLayers, true);
          else
            this.initTileLayers(this.layers[this.initial].tileLayers, false);

          var include;
          //check border toggle
          if (this.layers[this.initial].border)
            include = this.layers[this.initial].border.include != null ? this.layers[this.initial].border.include : false;
          else
            include = false;

          if (this.borders == true && include == false){
                this.borderLayer.remove(this.map);
                this.borders = false;
            }

          if(this.layers[this.initial].border){
            if (include == true){
                if (this.borderLayer){
                    this.borderLayer.remove(this.map);
                    this.borders = false;
                }
                this.borderCreation();
            }
          }
          //add json (WFS) layers
          if (this.layers[this.initial].jsonLayers){
            for(var i  = 0; i < this.layers[this.initial].jsonLayers.length;i++)
                this.initJsonLayers(this.json, this.layers[this.initial].jsonLayers[i], this.chart, this.layers[this.initial].chart,i);
          }
          //add spyglass
          if (this.spyglass){
              this.initSpyglass(this.spyglassID, this.spyglassLayer);
          }
      },
      //spyglass data
      initSpyglass(spyglassID){
            this.spyglassLayer = L.tileLayer.mask(this.layers[spyglassID].tileLayers[0].title, {
                maxZoom:11,
                minZoom:4,
                opacity:1,
                transparent:true,
                pane:"spyglass"
            });
            this.map.createPane('spyglass');
            this.map.getPane('spyglass').style.pointerEvents = 'auto';
            this.map.getPane('spyglass').style.zIndex = 600;
            this.spyglassLayer.addTo(this.map);

            var spy = this.spyglassLayer;
            this.map.on("mousemove", function(e) {
                if (spy)
                    spy.setCenter(e.containerPoint);
            });
      },
      //create tile layers
      initTileLayers(layer, slider){
          this.tiles = [];
          if (this.json == null)
            this.info.remove(this.map)
          if (layer){
            for(var i = 0; i < layer.length; i++){
                var layerType = layer[i].type != null ? layer[i].type : "geoserver";
                //add tilelayers dependent on a timeline slider
                if (slider == true){
                    if (layer[i].value[0] <= this.sliderValue && layer[i].value[1] >= this.sliderValue){
                        if (layerType == "geoserver"){
                            this.tiles[i] = L.tileLayer.wms('https://geoserver.library.oregonstate.edu/geoserver/PNW_ATLAS/wms', {
                                layers: layer[i].title,
                                format: 'image/png',
                                maxZoom:11,
                                minZoom:4,
                                opacity:layer[i].opacity,
                                transparent:true,
                                pane:'pane-' + i
                            });
                        }
                        else if (layerType == "mapbox"){
                            this.tiles[i] = L.tileLayer(layer[i].title, {
                                attribution:layer[i].attribution,
                                maxZoom:11,
                                minZoom:4,
                                opacity:layer[i].opacity,
                                transparent:true,
                                pane:'pane-' + i
                            });
                            document.getElementsByClassName("leaflet-control-attribution")[0].style.visibility = "visible";
                        }
                        else if (layerType == "rumsey"){
                            this.tiles[i] = L.tileLayer(layer[i].title, {
                                maxZoom:11,
                                minZoom:4,
                                opacity:1,
                                transparent:true,
                                pane:'pane-' + i
                            });
                        }

                        this.map.createPane('pane-' + i);
                        this.map.getPane('pane-' + i).style.pointerEvents = 'none';
                        this.map.getPane('pane-' + i).style.zIndex = layer[i].pane;
                        this.tiles[i].addTo(this.map);
                    }
                }
                //add tilelayers if they aren't dependant on a timeline slider
                else {
                    if (layerType == "geoserver"){
                        this.tiles[i] = L.tileLayer.wms('https://geoserver.library.oregonstate.edu/geoserver/PNW_ATLAS/wms', {
                            layers: layer[i].title,
                            format: 'image/png',
                            maxZoom:11,
                            minZoom:4,
                            opacity:layer[i].opacity,
                            transparent:true,
                            pane:'pane-' + i
                        });
                    }
                    else if (layerType == "mapbox"){
                        this.tiles[i] = L.tileLayer(layer[i].title, {
                            attribution:layer[i].attribution,
                            maxZoom:11,
                            minZoom:4,
                            opacity:layer[i].opacity,
                            transparent:true,
                            pane:'pane-' + i
                        });
                    }
                    else if (layerType == "rumsey"){
                        this.tiles[i] = L.tileLayer(layer[i].title, {
                            maxZoom:11,
                            minZoom:4,
                            opacity:1,
                            transparent:true,
                            pane:'pane-' + i
                        });
                    }
                    this.map.createPane('pane-' + i);
                    this.map.getPane('pane-' + i).style.pointerEvents = 'none';
                    this.map.getPane('pane-' + i).style.zIndex = layer[i].pane;
                    this.tiles[i].addTo(this.map);
                }
            }
         }
      },
      //reset layers when a new map is selected
      removeLayers(){
        for(var i = 0; i < this.tiles.length; i++){
              if(this.tiles[i])
                this.tiles[i].remove(this.map);
          }
        for(var i = 0; i < this.json.length; i++){
                this.json[i].remove(this.map);
          }
        if (this.selectionJson){
            for(var i = 0; i < this.selectionJson.length; i++){
                this.selectionJson[i].remove(this.map);
            }
        }
      },
      initJsonLayers(array, selection, chart, chartData, num){
           var self = this;
           var map = self.map;
           var url = selection.url; //json URL
           var layer = null;
///////////////////////////////////////////////////////////////variables/////////////////////////////////////////////////////////////////////////
                    /////////////////////data type variables///////////////////////////
                    var feature = selection.feature, //feature type (point, line, polygon)
                        type = selection.type != null ? selection.type : 'single'; //styling type (graduated, categorized, single, selection)
                    //////////////////////if there are attributes dependant on a slider////////////////////////
                    var sliderAttribute = selection.sliderAttribute != null ? selection.sliderAttribute : null;
                    if (sliderAttribute){
                        for (var i = 0; i < sliderAttribute.length; i++){
                            if (sliderAttribute[i].sliderValue == self.sliderValue){
                                var sliderField = sliderAttribute[i].attribute;
                                var sliderText = sliderAttribute[i].sliderValue;
                                var sliderNullText = sliderAttribute[i].nullText;
                                var sliderAttributeValues = sliderAttribute[i].values;
                            }
                        }
                    }
                    /////////////////////general and polygon styling variables///////////////////////////
                    var interactive = selection.interactive != null ? selection.interactive : true, //jsonLayer interactive (false if layer is non-interactive)
                        pane = selection.pane != null ? selection.pane : 'overlayPane',
                        field = selection.attribute != null ? selection.attribute : sliderField != null ? sliderField : 'N/A', //attribute field used for polygon styling
                        fillOpacity = selection.fillOpacity != null ? selection.fillOpacity : 0.7, //fill opacity style value
                        opacity = selection.opacity != null ? selection.opacity : 0.7, //opacity style value
                        colors = selection.colors != null ? selection.colors : ['#ffffff'], //line colors
                        fillColors = selection.fillColors != null ? selection.fillColors : ['#ffffff'], //fill colors
                        weight = selection.weight != null ? selection.weight :1, //outline weight variable if weight is static
                        values = selection.values != null ? selection.values : sliderAttributeValues ? sliderAttributeValues : null, //values used for data dependant color styling
                        opacityField = selection.opacityAttribute != null ? selection.opacityAttribute : field; //attritube bin values for coloring (string categories or graduated numbers)
                    /////////////////////point styling variables///////////////////////////
                    var radiusType = selection.radiusType != null ? selection.radiusType : 'single', //styling type (graduated, categorized, single) for circle size
                        radiusField = selection.radiusAttribute != null ? selection.radiusAttribute : sliderField != null ? sliderField : 'N/A', //attribute field used for radius styling
                        radiusValues = selection.radiusValues != null ? selection.radiusValues : sliderAttributeValues ? sliderAttributeValues : [1], //attribute bin values for radius styling
                        radius = selection.radius != null ? selection.radius : [8]; //radius size values
                    /////////////////////icon styling variables///////////////////////////
                    var icon = selection.icon != null ? selection.icon : null;
                    /////////////////////line styling variables///////////////////////////
                    var weightType = selection.weightType != null ? selection.weightType : 'single',
                        weightField = selection.weightAttribute != null ? selection.weightAttribute : sliderField != null ? sliderField : 'N/A', //attribute field used for weight styling
                        weightValues = selection.weightValues != null ? selection.weightValues : [1]; //attribute bin values for weight styling
                    /////////////////////dash styling variables///////////////////////////
                    var dashArray = selection.dashArray != null ? selection.dashArray : null,
                        dashField = selection.dashAttribute != null ? selection.dashAttribute : sliderField != null ? sliderField : 'N/A', //attribute field used for radius styling
                        dashValues = selection.dashValues != null ? selection.dashValues : null, //attribute bin values for radius styling
                        dashType = selection.dashType != null ? selection.dashType : 'single'; //radius size values
                    /////////////////////spiderfy///////////////////////////
                    var spiderfy = selection.spiderfy != null ? selection.spiderfy : false;
                    if (spiderfy == true)
                        self.oms = new OverlappingMarkerSpiderfier(map, {legWeight:1.25});
                    /////////////////////hover styling variables///////////////////////////
                    var hover = selection.hover != null ? selection.hover : true, //change of styling on hover
                        hoverOpacity = selection.hoverOpacity != null ? selection.hoverOpacity : 0.9, //fill opacity on hover
                        hoverLineOpacity = selection.hoverLineOpacity != null ? selection.hoverLineOpacity : opacity, //outline/line opacity on hover
                        hoverColor = selection.hoverColor != null ? selection.hoverColor : colors, //outline/line color on hover
                        hoverDash = selection.hoverDash != null ? selection.hoverDash : dashArray, //dash array on hover
                        hoverPosition = selection.hoverPosition != null ? selection.hoverPosition : null, //controls if the feature is brought to the front of the pane on hover
                        titleVariable = selection.titleVariable != null ? selection.titleVariable : null, //title of chart if it changes on hover
                        hoverText = selection.hoverText != null ? selection.hoverText : null, //title of chart if it changes on hover
                        hoverVar = selection.hoverVar != null ? selection.hoverVar : null, //variable to change title of chart if it changes on hover
                        hoverRadius = selection.hoverRadius != null ? selection.hoverRadius : radius, //change point radius on hover
                        hoverWeight = selection.hoverWeight != null ? selection.hoverWeight : weight, //change line/outline width on hover
                        hoverFill = selection.hoverFill != null ? selection.hoverFill : null; //if hoverfill == 'invert', fill color and color values will be swapped when the layer is hovered
                    /////////////////////filter variables///////////////////////////
                    //used if a layer display is being filterd
                    if (selection.filterVariable){
                        var filterVariable = selection.filterVariable != null ? selection.filterVariable : null,//attribute used for filtering
                            filterValue = selection.filterValue != null && self.filterValue == null ? selection.filterValue : self.filterValue != null ? self.filterValue : null,//value to be filtered
                            secondFilterVariable = selection.secondFilterVariable != null ? selection.secondFilterVariable : null,//second attribute used for filtering
                            secondFilterValue = selection.secondFilterValue != null ? selection.secondFilterValue : null,//second value to be filtered
                            filterType = selection.filterType != null ? selection.filterType : 'equal',//type of filter
                            filterValueSuffix = selection.filterValueSuffix != null ? selection.filterValueSuffix : null;

                        if (filterValueSuffix)
                            self.sliderValue = parseInt(self.sliderValue + filterValueSuffix, 10);
                    }
                    //used if a second dropdown menu is used to filter variables
                    if (selection.secondDropdownValues){
                        var secondValues = new Array(values.length);
                        var secondInitial;
                        for (var k = 0; k < this.secondDropdownButtons.buttons.length; k++){
                            if (this.secondDropdownButtons.buttons[k].filter == filterValue){
                                secondInitial = k;
                            }
                        }
                        for (var k = 0; k < values.length; k++){
                            secondValues[k] = this.secondDropdownButtons.buttons[secondInitial].values[values[k]];
                        }
                        values = secondValues;
                    }
                    /////////////////////selection type variables///////////////////////////
                    //used when users can select a geographic feature to display information about that feature in the legend
                    if (type == 'selection' || type == 'selectionCat' || type == 'selectionGrad' || type == 'selectionDiv'){
                        var selectionOpacity = selection.selection.selectionOpacity != null ? selection.selection.selectionOpacity : fillOpacity,//fill opacity of selected feature
                        selectionLineOpacity = selection.selection.selectionLineOpacity != null ? selection.selection.selectionLineOpacity : opacity,//line/outline opacity of selected feature
                        selectionHover = selection.selection.selectionHover != null ? selection.selection.selectionHover : hoverOpacity,//fillOpacity when a selected feature is hovered
                        unselectedOpacity = selection.selection.unselectedOpacity != null ? selection.selection.unselectedOpacity : fillOpacity,//fillOpacity of unselected features
                        unselectedHover = selection.selection.unselectedHover != null ? selection.selection.unselectedHover : hoverOpacity,//fillOpacity when an unselected feature is hovered
                        titleVar = selection.selection.titleVar != null ? selection.selection.titleVar : null, //attribute used for selected feature title in the legend
                        titleSuffix = selection.selection.titleSuffix != null ? selection.selection.titleSuffix : "", //used if the title has a string suffix (ex. "County")
                        subtitleVar = selection.selection.subtitleVar != null ? selection.selection.subtitleVar : null,//attribute used for the subtitle in the legend
                        varList = selection.selection.varList != null ? selection.selection.varList : null,//array of attributes if statistics about a feature are displayed in the legend
                        varLabel = selection.selection.varLabel != null ? selection.selection.varLabel : null,//array of text to accompany attributes if statistics about a feature are displayed in the legend
                        selectionVar = selection.selection.filterVar != null ? selection.selection.filterVar : null,//variable used for a timeline slider
                        selectionLayers = selection.selection.selectionLayers != null ? selection.selection.selectionLayers : null,//used if selecting a feature will add other json layers to the map
                        selectionOffset = selection.selection.selectionOffset != null ? selection.selection.selectionOffset : 0.2,//when map zooms onto selected feature, how much map offset to apply
                        clearLayer = selection.selection.clearLayer != null ? selection.selection.clearLayer : null,//used if json layers need to be removed
                        imageLink = selection.selection.imageLink != null ? selection.selection.imageLink : null;//used if an image opens upon feature selection
                    }
                    /////////////////////icon styling variables///////////////////////////
                    self.activeLayer[num] = selection.activeLayer != null ? selection.activeLayer : null;
                    /////////////////////tooltip/info interactivity variables///////////////////////////
                    var includeInfo = selection.info != null ? selection.info : interactive == false ? false : true, //info control included
                        tooltipType = selection.tooltipType != null ? selection.tooltipType : null,
                        tooltipOffset = selection.tooltipOffset != null ?  L.point(selection.tooltipOffset[0],selection.tooltipOffset[1]) : L.point(5,0),//tooltip offset in pixels
                        nullText = selection.nullText != null ? selection.nullText : sliderNullText != null ? sliderNullText : 'N/A';//text to display when an attribute is null

                    self.sticky[num] = selection.sticky != null ? selection.sticky : false;//does the tooltip display in feature center or follow the cursor?

                        if (feature == 'polygon' && includeInfo == true){
                            self.tool[num] = selection.tooltip != null ? selection.tooltip : 'info'; //set interactivity type, 'info' control is default for polygon
                            this.info.addTo(this.map);
                        }
                        else if ((feature == 'point' || feature == 'line') && includeInfo == true){
                            this.info.addTo(this.map);
                            self.tool[num] = selection.tooltip != null ? selection.tooltip : 'tooltip'; //set interactivity type, 'tooltip' control is default for point
                        }
                        else{
                            self.tool[num] = 'none';
                        }
                        /////////////////////interactive text setup///////////////////////////
                            self.tooltipText.push(new Object());
                            self.tooltipVars.push(new Object());
                            self.tooltipText[num].text = selection.tooltipText != null ? selection.tooltipText : null; //tooltip/info text
                            self.tooltipVars[num].vars = selection.tooltipVars != null ? selection.tooltipVars : null; //tooltip/info variables
                        /////////////////////override null values//////////////////////////
                        function nullFinder(value) {
                            if (value == null)
                                return nullText;
                            else{
                                if (isNaN(value))
                                    return value;
                                else
                                    return value.toLocaleString(undefined, {minimumFractionDigits: 0, maximumFractionDigits: 2});
                            }
                        }
                        var isSelection = selection.isSelection != null ? true : false;
                        /////////////////////tooltip/info text setup///////////////////////////
                        function tooltip(props, index){
                            var tooltip;
                            for (var j = 0; j < self.tooltipText[index].text.length; j++){
                                if (j == 0)
                                     tooltip = self.tooltipText[index].text[j] + nullFinder(Object.byString(props,self.tooltipVars[index].vars[j]));
                                else if ((self.tooltipText[index].text.length - 1) != j)
                                    if (tooltipType == 'filterTooltip'){
                                        if (Object.byString(props,self.tooltipVars[index].vars[j]) != 0)
                                             tooltip += self.tooltipText[index].text[j] + nullFinder(Object.byString(props,self.tooltipVars[index].vars[j]));
                                    }
                                    else if (self.tooltipVars[index].vars[j] == 'sliderText')
                                        tooltip += self.tooltipText[index].text[j] + sliderText;
                                    else if (self.tooltipVars[index].vars[j] == 'sliderAttribute'){
                                        //console.log(Object.byString(props,sliderField));
                                        tooltip += self.tooltipText[index].text[j] + nullFinder(Object.byString(props,sliderField));
                                    }
                                    else
                                        tooltip += self.tooltipText[index].text[j] + nullFinder(Object.byString(props,self.tooltipVars[index].vars[j]));
                                else
                                    tooltip += self.tooltipText[index].text[j];
                            }
                            return tooltip;
                        }
                        /////////////////////info control setup///////////////////////////
                        var infoHover = selection.infoHover != null ? selection.infoHover : '<b>Hover over Map</b>', //default display when no features are hovered
                            infoWidth = this.layers[this.initial].infoWidth != null ? this.layers[this.initial].infoWidth : 'auto',
                            info  = this.info;
                            info.update = function (props, num) {
                                if (self.tool[num] == 'info' && self.tooltipText[num].text != null){
                                    this._div.innerHTML = (props ?
                                            tooltip(props, num):
                                        infoHover);
                                }
                                else{
                                    this._div.innerHTML = (
                                        infoHover);
                                }
                            };
                            if (includeInfo == true)
                               info.update();

                        var fillPattern;

                        if (type == 'pattern'){
                            fillPattern = new L.StripePattern({
                                    weight: selection.pattern.weight,
                                    spaceWeight: selection.pattern.spaceWeight,
                                    height: selection.pattern.height,
                                    angle: selection.pattern.angle,
                                    color: selection.pattern.color,
                                    spaceColor: selection.pattern.spaceColor,
                                });
                            fillPattern.addTo(map);
                        }

                    var flow = selection.flow != null ? selection.flow : null;
/////////////////////////////////////////function used to create point icons when they are used//////////////////////////////
                    function setIcon(iconValues, iconUrl, iconSize, attribute){
                        if (iconValues){
                            for (var k = 0; k < iconValues.length; k++){
                                if (Object.byString(attribute, icon.iconAttribute) == iconValues[k]){
                                    return L.icon({iconSize: [iconSize[k],iconSize[k]], iconUrl:self.getURL(iconUrl[k])});
                                }
                            }
                        }
                        else
                            return L.icon({iconSize: [iconSize,iconSize], iconUrl:self.getURL(iconUrl)});
                    }
///////////////////////////////////////////////////////////////map setup/////////////////////////////////////////////////////////////////////////
                    //default map position
                    if (selection.position){
                        var width = document.documentElement.clientWidth;
                        if (width < 1500 && width > 800)
                            this.map.setView(new L.LatLng(selection.position.lat, selection.position.lon - 2), selection.position.zoom);
                        else if (width > 1501)
                            this.map.setView(new L.LatLng(selection.position.lat, selection.position.lon), selection.position.zoom + 1);
                        else
                            this.map.setZoom(selection.position.zoom - 1);

                    }
                    //point styling
                    if (feature == 'point'){
                        layer = new L.TopoJSON(null, {
                            pointToLayer: function (feature, latlng) {
                                //if points are styled as div markers
                                if (type == 'pointDivGrad' || type == 'pointDivCat' || type == 'selectionDiv'){
                                    if (self.tool[num] == 'tooltip'){
                                        return L.marker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                            icon:L.divIcon({className:"iconTest",html:`<div style="${selection.divIcon}; height:inherit; width:inherit; background-color: ${setValue(Object.byString(feature.properties, field),fillColors, values, type)};"/>`, iconSize:[setValue(Object.byString(feature.properties, radiusField),radius, radiusValues, radiusType),setValue(Object.byString(feature.properties, radiusField),radius, radiusValues, radiusType)]})
                                        }).bindTooltip(tooltip(feature.properties, num), {className: 'label-tooltip',offset:tooltipOffset, direction:'right', pane:'tooltip',sticky:self.sticky[num]});
                                    }
                                    else {
                                        return L.marker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                            icon:L.divIcon({className:"iconTest",html:`<div style="${selection.divIcon}; height:inherit; width:inherit; background-color: ${setValue(Object.byString(feature.properties, field),fillColors, values, type)};"/>`, iconSize:[setValue(Object.byString(feature.properties, radiusField),radius, radiusValues, radiusType),setValue(Object.byString(feature.properties, radiusField),radius, radiusValues, radiusType)]})
                                        });
                                    }
                                }
                                //if points are styled as png icons
                                else if(type == "icon"){
                                    if (self.tool[num] == 'tooltip'){
                                        return L.marker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                            icon:setIcon(icon.iconValues, icon.iconUrl, icon.iconSize, feature.properties)
                                        }).bindTooltip(tooltip(feature.properties, num), {className: 'label-tooltip',offset:tooltipOffset, direction:'right', pane:'tooltip',sticky:self.sticky[num]});
                                    }
                                    else {
                                        return L.marker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                            icon:catIcon(icon.iconValues, icon.iconUrl, icon.iconSize, feature.properties)
                                        });
                                    }
                                }
                                //if points are styled as circleMarkers
                                else{
                                    if (self.tool[num] == 'tooltip'){
                                        return L.circleMarker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                        }).bindTooltip(tooltip(feature.properties, num), {className: 'label-tooltip',offset:tooltipOffset, direction:'right', pane:'tooltip',sticky:self.sticky[num]});
                                    }
                                    else {
                                        return L.circleMarker(latlng, {
                                            interactive:interactive,
                                            pane:pane,
                                        });
                                    }
                                }
                            },
                            onEachFeature: function(feature, layer) {
                                ///activate spiderfy plugin
                                if (spiderfy == true)
                                    self.oms.addMarker(layer);
                                //set new icons for hover if points are being styled as icons
                                if (type == 'icon'){
                                    layer.on("mouseover",function(e){
                                        layer.setIcon(setIcon(icon.iconValues, icon.iconHover, icon.iconHoverSize, feature.properties))
                                    });
                                    layer.on("mouseout",function(e){
                                        layer.setIcon(setIcon(icon.iconValues, icon.iconUrl, icon.iconSize, feature.properties))
                                    });
                                }
                            },
                            filter:filter
                        });
                    }
                    //polygon/line styling
                    if (feature == 'polygon' || feature == 'line'){
                        layer = new L.TopoJSON(null,{
                            pane:pane,
                            interactive:interactive,
                            onEachFeature: function(feature, featureLayer){
                                if (self.tool[num] == 'tooltip'){
                                  featureLayer.bindTooltip(tooltip(feature.properties, num), {className: 'label-tooltip',offset:tooltipOffset, direction:'right', pane:'tooltip',sticky:self.sticky[num]});
                                }
                              },
                            filter:filter
                        });
                    }
                    //function used to filter features
                    function filter(feature){
                        if (filterVariable){
                            if (filterType == 'equal'){
                                if (filterValue || self.sliderValue){
                                    if (Object.byString(feature.properties, filterVariable) == self.sliderValue || Object.byString(feature.properties, filterVariable) == filterValue)
                                        if (secondFilterVariable){
                                            if (Object.byString(feature.properties, secondFilterVariable) == secondFilterValue || Object.byString(feature.properties, secondFilterVariable) == self.sliderValue)
                                                return true
                                        }
                                        else
                                            return true
                                }
                                else
                                    return true
                            }
                            if (filterType == 'greaterLesser'){
                                if (Object.byString(feature.properties, filterVariable) <= self.sliderValue && Object.byString(feature.properties, secondFilterVariable) >= self.sliderValue){
                                    return true;
                                }
                            }
                            if (filterType == 'lesser'){
                                if (Object.byString(feature.properties, filterVariable) <= self.sliderValue){
                                    return true;
                                }
                            }
                            if (filterType == 'doesNotEqual'){
                                if (Object.byString(feature.properties, filterVariable) != filterValue){
                                    //console.log(filterValue);
                                    return true;
                                }
                            }
                        }
                        else
                            return true;
                    }
                    //used for heat layers
                    if (feature == 'heat'){
                        var heat_array = [0,0,0],
                            blur = selection.blur,
                            gradient = selection.gradient,
                            minOpacity = selection.minOpacity != null ? selection.minOpacity : 0,
                            heatLayer;
                        if (self.counter == 1){
                            var csvUrl = selection.csvUrl;
                            $.ajax({
                                type: "GET",
                                url: csvUrl,
                                dataType: "text",
                                success: function(data){
                                    var obj = $.csv.toObjects(data);
                                    for (var i = 0; i < obj.length; i++) {
                                        heat_array[i] = [obj[i].lat, obj[i].lon, obj[i].mag];
                                    }
                                    heatLayer = heat_array.map(function (p) { return [p[0], p[1]]; });
                                    layer = L.heatLayer(heatLayer,{blur:blur,gradient:gradient, minOpacity:minOpacity});

                                    array[num] = layer;
                                    array[num].addTo(map);
                                }
                            });
                        }
                        else{
                            var heatName = eval(selection.heatName);
                            heatLayer = heatName.map(function (p) { return [p[0], p[1]]; });
                            layer = L.heatLayer(heatLayer,{blur:blur,gradient:gradient, minOpacity:minOpacity});

                            array[num] = layer;
                            array[num].addTo(map);
                        }

                    }
                    //used for all layers that aren't heat layers
                    if (feature != 'heat'){
                        $.getJSON(url, function(data){
                            addTopoData(data);
                        });
                        function addTopoData(topoData) {
                            layer.addData(topoData);
                            if (type != 'pointDivCat' && type != 'pointDivGrad' && type != 'selectionDiv' && type != 'icon')
                                layer.eachLayer(handleLayer);
                        }
                    }

                 /////////////////////map styling///////////////////////////

                    function handleLayer(layer){
                        //used for leaflet.pattern layers
                        if (type == 'pattern'){
                            layer.setStyle({
                                fillPattern:fillPattern,
                                weight: weight,
                                opacity:opacity,
                                fillOpacity:fillOpacity,
                                color:colors[0],
                            });
                        }
                        else{
                            layer.setStyle({
                                fillColor: setValue(Object.byString(layer.feature.properties, field),fillColors, values, type),
                                fillOpacity: setValue(Object.byString(layer.feature.properties, opacityField),fillOpacity, values, type),
                                weight: setValue(Object.byString(layer.feature.properties, weightField), weight, weightValues, weightType),
                                opacity:setValue(Object.byString(layer.feature.properties, opacityField),opacity, values, type),
                                radius:setValue(Object.byString(layer.feature.properties, radiusField), radius, radiusValues, radiusType),
                                color:setValue(Object.byString(layer.feature.properties, field),colors, values, type),
                                dashArray:setValue(Object.byString(layer.feature.properties, dashField),dashArray, dashValues, dashType),
                            });
                        }
                        layer.on({
                            mouseover: highlight,
                            mouseout: resetHighlight,
                            click:click
                        });
                    }
                    /////////////////////styling function///////////////////////////
                        function setValue(num, output, bin, typeField){
                            if (Array.isArray(output)){
                                if (typeField =='single' || typeField == 'selection'){
                                    return output[0];
                                }
                                if (typeField == 'graduated' || typeField == 'pointDivGrad' || typeField == 'selectionDiv' || typeField == 'selectionGrad'){
                                    if (output.length > 1){
                                        return  num > bin[0] ? output[0]:
                                            num > bin[1] ? output[1]:
                                            num > bin[2] ? output[2]:
                                            num  > bin[3] ? output[3]:
                                            num  > bin[4] ? output[4]:
                                            num  > bin[5] ? output[5]:
                                                output[output.length - 1];
                                    }
                                    else{
                                        return output[0];
                                    }
                                }
                                if (typeField == 'categorized' || typeField == 'pointDivCat' || typeField == 'selectionCat'){
                                    if (output.length > 1){
                                        return  num == bin[0] ? output[0]:
                                            num == bin[1] ? output[1]:
                                            num == bin[2] ? output[2]:
                                            num == bin[3] ? output[3]:
                                            num == bin[4] ? output[4]:
                                            num == bin[5] ? output[5]:
                                            num == bin[6] ? output[6]:
                                            num == bin[7] ? output[7]:
                                            num == bin[8] ? output[8]:
                                            num == bin[9] ? output[9]:
                                            num == bin[10] ? output[10]:
                                            num == bin[11] ? output[11]:
                                            num == bin[12] ? output[12]:
                                            num == bin[13] ? output[13]:
                                            num == bin[14] ? output[14]:
                                            num == bin[15] ? output[15]:
                                                output[output.length - 1];
                                    }
                                    else{
                                        return output[0];
                                    }
                                }
                            }
                            else
                                return output;
                        }

                /////////////////////click setup///////////////////////////
                var selectedFeature;
                function click(){
                    if (type == 'selection' || type == 'selectionCat' || type == 'selectionGrad'){
                        //share selection with other components
                        self.$emit('featureSelected');
                        //update chart
                        if (chartData){
                            chartUpdate(this.feature.properties);
                        }
                        //set style for selected feature
                        if (selectedFeature){
                            selectedFeature.setStyle ({
                                fillOpacity: setValue(Object.byString(selectedFeature.feature.properties, opacityField),fillOpacity, values, type),
                                dashArray:setValue(Object.byString(selectedFeature.feature.properties, dashField),dashArray, dashValues, dashType),
                                opacity:setValue(Object.byString(selectedFeature.feature.properties, opacityField),opacity, values, type),
                                color: setValue(Object.byString(selectedFeature.feature.properties, field), colors, values, type),
                                radius: setValue(Object.byString(selectedFeature.feature.properties, radiusField), radius, radiusValues, radiusType),
                                weight: setValue(Object.byString(selectedFeature.feature.properties, weightField), weight, weightValues, weightType)
                            });
                        }

                        selectedFeature = this;
                        //set style for unselected features
                        layer.setStyle ({
                            fillOpacity: unselectedOpacity
                        });
                        //set opacity for selected feature
                        this.setStyle ({
                            fillOpacity: selectionOpacity,
                            opacity: selectionLineOpacity
                        });
                        //zoom to selected feature
                        if (this.feature.properties.y1 != null){
                            self.map.fitBounds([
                                [this.feature.properties.y1, this.feature.properties.x1 - selectionOffset],
                                [this.feature.properties.y2, this.feature.properties.x2 - selectionOffset]
                            ]);
                        }
                        //activate appropriate container in legend
                        $('.selectionContainer').css('display','block');
                        $('#selectionTitle-'+self.initial).html(Object.byString(this.feature.properties, titleVar) + titleSuffix);
                        //activate list of statistics in legend
                        if (varList){
                            for (var i = 0; i < varList.length; i++){
                                var listID = "varContent-" + i;
                                document.getElementById(listID).innerHTML = Object.byString(this.feature.properties, varList[i]).toLocaleString(undefined, {minimumFractionDigits: 0, maximumFractionDigits: 0});
                            }
                        }
                        //add additional layers upon selection
                        if (selectionLayers){
                            if (clearLayer)
                                self.json[clearLayer].remove(map);
                            for(var j = 0; j < self.selectionJson.length; j++)
                                self.selectionJson[j].remove(map);
                            self.sliderValue = Object.byString(this.feature.properties, selectionVar);
                            for (var j = 0; j < selectionLayers.length; j++){
                                self.initJsonLayers(self.selectionJson, selection.selection.selectionLayers[j], chart, chartData, j);
                            }
                        }
                        //add subtitle in legend
                        if (subtitleVar)
                            $('#selectionSubtitle').html(Object.byString(this.feature.properties, subtitleVar));
                        //add image in selection
                        if (imageLink){
                            $('#selectionImage').attr("src",self.getURL(Object.byString(this.feature.properties, imageLink)));
                        }
                    }
                    //add flowlayer
                    if (flow){
                        self.flowSelection = Object.byString(this.feature.properties, flow.flow_id);
                        self.flowLayer(flow, self.flowCanvas);
                    }
                    //used to display different legends upon selection
                    var isSelectedDiv = document.getElementsByClassName("selected");
                    var isUnSelectedDiv = document.getElementsByClassName("unselected");
                    if (isUnSelectedDiv.length != 0){
                        for (var k = 0; k < isUnSelectedDiv.length; k++)
                            isUnSelectedDiv[k].style.display = "none";
                    }
                    if (isSelectedDiv.length != 0){
                        for (var k = 0; k < isSelectedDiv.length; k++)
                            isSelectedDiv[k].style.display = "block";
                    }

                }
                /////////////////////hover setup///////////////////////////
                    //activate chart for point div layers
                    layer.on('tooltipopen', function(e){
                        if (type == "pointDivGrad" || type == "pointDivCat"){
                            if (chartData){
                                if (self.activeLayer[num]){
                                    for (var i = 0; i < chartData.length; i++){
                                        if (chartData[i].chartLoc != 'legendClick'){
                                            chartUpdate(e.tooltip._source.feature.properties);
                                        }
                                    }
                                }
                            }
                        }
                    });
                    layer.on('tooltipclose', function(e){
                        if (type == "pointDivGrad" || type == "pointDivCat"){
                            if (chartData){
                                for (var i = 0; i < chartData.length; i++){
                                    if (chartData[i].chartLoc == 'tooltip'){
                                        if( $('#' + self.layers[self.initial].legend[chartData[i].id].chartID).length ){
                                            removeElement(self.layers[self.initial].legend[chartData[i].id].chartID);
                                        }
                                    }
                                }
                            }
                        }
                    });
                    //highlight layer
                    function highlight(){
                        if (feature == 'polygon' && hoverDash != null && hoverPosition == null)
                            this.bringToFront();

                        if (hover == true){
                            //style hovered layer
                            function setHoverStyle(highlightLayer){
                                highlightLayer.setStyle({
                                    fillOpacity: setValue(Object.byString(highlightLayer.feature.properties, opacityField),hoverOpacity, values, type),
                                    opacity: setValue(Object.byString(highlightLayer.feature.properties, opacityField),hoverLineOpacity, values, type),
                                    dashArray:setValue(Object.byString(highlightLayer.feature.properties, dashField),hoverDash, dashValues, dashType),
                                    color: setValue(Object.byString(highlightLayer.feature.properties, field), hoverColor, values, type),
                                    weight: setValue(Object.byString(highlightLayer.feature.properties, weightField), hoverWeight, weightValues, weightType)
                                });
                                if (hoverFill == 'invert'){
                                    highlightLayer.setStyle({
                                        color: fillColors,
                                        fillColor: colors
                                    });
                                }
                            }
                            //style selected features on hover
                            if (selectedFeature){
                                if (selectedFeature != this)
                                    setHoverStyle(this);
                                else{
                                    this.setStyle({
                                        fillOpacity: selectionHover,
                                    });
                                }
                            }
                            else
                                setHoverStyle(this);

                            //update info control for polygon layers
                            if (self.tool[num] == 'info' && includeInfo == true){
                                $('.info').css("width",infoWidth);
                                info.update(this.feature.properties, num);
                            }

                            //activate chart in tooltip or info control
                            if (chartData){
                                if (titleVariable)
                                    $('#chartTitle').html(Object.byString(this.feature.properties, titleVariable));

                                for (var i = 0; i < chartData.length; i++){
                                    if (chartData[i].chartLoc != 'legendClick'){
                                        chartUpdate(this.feature.properties);
                                    }
                                }
                            }

                            if (hoverText)
                                $('#hoverContainer').html(hoverText[0] + Object.byString(this.feature.properties, hoverVar) + hoverText[1])

                        }
                    }
                    function removeElement(id) {
                        var elem = document.getElementById(id);
                        return elem.parentNode.removeChild(elem);
                    }
                    //reset layer on mouse out
                    function resetHighlight(){
                        //clear chart data
                        if (chartData){
                            for (var i = 0; i < chartData.length; i++){
                                if (chartData[i].chartLoc == 'tooltip'){
                                    removeElement(self.layers[self.initial].legend[chartData[i].id].chartID);
                                }
                            }
                        }
                        if (hover == true){
                            if (selectedFeature != this){
                                if (selectedFeature){
                                    this.setStyle({
                                        fillOpacity: unselectedOpacity,
                                        dashArray:setValue(Object.byString(this.feature.properties, dashField),dashArray, dashValues, dashType),
                                        opacity:setValue(Object.byString(this.feature.properties, opacityField),opacity, values, type),
                                        color: setValue(Object.byString(this.feature.properties, field), colors, values, type),
                                        radius: setValue(Object.byString(this.feature.properties, radiusField), radius, radiusValues, radiusType),
                                        weight: setValue(Object.byString(this.feature.properties, weightField), weight, weightValues, weightType)
                                    });
                                }
                                else{
                                    this.setStyle({
                                        fillColor: setValue(Object.byString(this.feature.properties, field),fillColors, values, type),
                                        fillOpacity: setValue(Object.byString(this.feature.properties, opacityField),fillOpacity, values, type),
                                        dashArray:setValue(Object.byString(this.feature.properties, dashField),dashArray, dashValues, dashType),
                                        opacity:setValue(Object.byString(this.feature.properties, opacityField),opacity, values, type),
                                        color: setValue(Object.byString(this.feature.properties, field), colors, values, type),
                                        radius: setValue(Object.byString(this.feature.properties, radiusField), radius, radiusValues, radiusType),
                                        weight: setValue(Object.byString(this.feature.properties, weightField), weight, weightValues, weightType)
                                    });
                                }
                            }
                            if (self.tool[num] == 'info' && includeInfo == true){
                                $('.info').css("width","auto");
                                info.update();
                            }
                            if (hoverText){
                                $('#hoverContainer').html("");
                            }
                        }
                    }
                /////////////////////chart update setup///////////////////////////
                    //update chart data
                    function chartUpdate(props){
                        for (var k = 0; k < chartData.length; k++)
                            //if chart is not located in the legend or in the tooltip and is activated on hover
                            if (chartData[k].chartLoc != 'legend' && chartData[k].chartLoc != 'tooltip'){
                                //remove old chart
                                if (self.chart[k])
                                    self.chart[k].destroy();
                                //initialize new chart
                                self.initChart(self.layers[self.initial].legend[k].chartID,chartData[k],k);
                                //if chart only has one data field (as in for a percentage)
                                if(chartData[k].data.length == 1){
                                    self.chart[k].data.datasets[0].data[0] = Object.byString(props, chartData[k].data[0]);
                                    self.chart[k].data.datasets[0].data[1] = 100 - Object.byString(props, chartData[k].data[0]);
                                }
                                //upate chart data
                                else{
                                    for (var j = 0; j < chartData[k].data.length; j++){
                                        self.chart[k].data.datasets[0].data[j] = Object.byString(props, chartData[k].data[j]);
                                    }
                                }

                                self.chart[k].update();
                            }
                            //if chart is located in the tooltip and is activated on hover
                            else if(chartData[k].chartLoc == 'tooltip'){
                                //initialize new chart
                                self.initChart(self.layers[self.initial].legend[k].chartID,chartData[k],k);
                                //if chart only has one data field (as in for a percentage)
                                if (chartData[k].data.length == 1){
                                    self.chart[k].data.datasets[0].data[0] = Object.byString(props, chartData[k].data[0]);
                                    self.chart[k].data.datasets[0].data[1] = 100 - Object.byString(props, chartData[k].data[0]);
                                }
                                //upate chart data
                                else{
                                    for (var j = 0; j < chartData[k].data.length; j++){
                                        self.chart[k].data.datasets[0].data[j] = Object.byString(props, chartData[k].data[j]);
                                        if (self.chart[k].data.datasets[1])
                                            self.chart[k].data.datasets[1].data[j] = Object.byString(props, chartData[k].data2[j]);
                                    }
                                }

                                self.chart[k].update();
                            }
                            else{
                                if(chartData[k].data.length == 1){
                                    self.chart[k].data.datasets[0].data[0] = Object.byString(props, chartData[k].data[0]);
                                    self.chart[k].data.datasets[0].data[1] = 100 - Object.byString(props, chartData[k].data[0]);
                                }
                                else{
                                    for (var j = 0; j < chartData[k].data.length; j++){
                                        self.chart[k].data.datasets[0].data[j] = Object.byString(props, chartData[k].data[j]);
                                    }
                                }
                                self.chart[k].update();
                            }
                    }
                //add features to map
                if (feature != 'heat'){
                    array[num] = layer;
                    array[num].addTo(map);
                }

                self.url = url;
        },
        //add flow layer
        flowLayer(flow, flowCanvas){

            if (flowCanvas)
                flowCanvas.clearAllPathSelections();

            var self = this;
            var canvasRenderer = L.canvas();
            Papa.parse(flow.flowURL, {
            download: true,
            header: true,
            dynamicTyping: true,
            skipEmptyLines: true,
            complete: function(results) {
                var geoJsonFeatureCollection = {
                type: 'FeatureCollection',
                features: results.data.map(function(datum) {
                    return {
                    type: 'Feature',
                    geometry: {
                        type: 'Point',
                        coordinates: [Object.byString(datum, flow.sourceCoordinates[0]), Object.byString(datum, flow.sourceCoordinates[1])]
                    },
                    properties: datum
                    }
                })
                };

                flowCanvas = L.canvasFlowmapLayer(geoJsonFeatureCollection, {
                    originAndDestinationFieldIds: {
                        originUniqueIdField: flow.source,
                        originGeometry: {
                        x: flow.sourceCoordinates[0],
                        y: flow.sourceCoordinates[1]
                        },
                        destinationUniqueIdField: flow.target,
                        destinationGeometry: {
                        x: flow.targetCoordinates[0],
                        y: flow.targetCoordinates[1]
                        }
                    },
                    canvasBezierStyle: flow.canvasBezierStyle,
                    style: function(geoJsonFeature) {
                        if (geoJsonFeature.properties.isOrigin) {
                            return {
                                renderer: canvasRenderer, // recommended to use L.canvas()
                                radius: 0,
                                weight: 0,
                                color: 'rgb(0, 0, 0,0)',
                                fillColor: 'rgba(0, 0, 0, 0)',
                                fillOpacity: 0
                            };
                        } else {
                            return {
                                renderer: canvasRenderer,
                                radius: 0,
                                weight: 0,
                                color: 'rgb(0, 0, 0,0)',
                                fillColor: 'rgba(0, 0, 0, 0)',
                                fillOpacity: 0.0
                            };
                        }
                    },
                pathDisplayMode: 'selection'
                });

                flowCanvas.addTo(self.map);
                flowCanvas.selectFeaturesForPathDisplayById(flow.target, self.flowSelection, false, 'SELECTION_NEW');

                self.flowCanvas = flowCanvas;
                }

            });
        },
        //initialize chart (chart.js)
        initChart(ID, chartData, num){
            //set chart ID
            var ctx = document.getElementById(ID).getContext('2d');
            //for pie and donut charts
            if (chartData.chartType != 'radar' && chartData.chartType != 'line'){
                var options =  {
                    type: chartData.chartType,
                        data: {
                            labels: ["Data1","Data2"],
                            datasets: [{
                                data: [50,50],
                                backgroundColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                                borderColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                            borderWidth: [0,0]
                        }]
                    },
                    options: chartData.options
                }
            }
            //for line charts
            else if(chartData.chartType == 'line'){
                var options =  {
                    type: chartData.chartType,
                        data: {
                            labels: ["Data1","Data2"],
                            datasets: [{
                                label:chartData.datasetLabel[0],
                                data: [50,50],
                                yAxisID:'val',
                                backgroundColor:['rgba(191, 64, 64,0.8)'],
                                borderColor:['rgba(191, 64, 64,0.8)'],
                                fill:false
                            },
                            {
                                label:chartData.datasetLabel[1],
                                data: [50,50],
                                yAxisID:'lbs',
                                backgroundColor:['rgba(245, 203, 112,0.8)'],
                                borderColor:['rgba(245, 203, 112,0.8)'],
                                fill:false
                            }]
                    },
                    options: chartData.options
                }
            }
            //for radar charts
            else{
                var options =  {
                    type: chartData.chartType,
                        data: {
                            labels: ["Data1","Data2"],
                            datasets: [{
                                label:chartData.datasetLabel[0],
                                data: [50,50],
                                backgroundColor: [
                                    chartData.backgroundColor[0]
            		            ],
                                borderColor: [
                		            chartData.colors[0],
            		            ],
                                pointBackgroundColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                                pointBorderColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                            borderWidth: 0
                            },
                            {
                                label:chartData.datasetLabel[1],
                                data: [50,50],
                                backgroundColor: [
                		            chartData.backgroundColor[1]
            		            ],
                                borderColor: [
                		            chartData.colors[1]
            		            ],
                                pointBackgroundColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                                pointBorderColor: [
                                    '#ffffff',
                                    '#ffffff'
                                ],
                            borderWidth: 0
                            }]
                    },
                    options: chartData.options
                }
            }
            //populate pie and donut charts
            if (chartData.chartType != 'radar' && chartData.chartType != 'line'){
                for (var i = 0; i < chartData.labels.length; i++){
                    options.data.labels[i] = chartData.labels[i];
                    if (chartData.pattern){
                        if (chartData.pattern.data == i){
                            options.data.datasets[0].backgroundColor[i] = pattern.draw(chartData.pattern.type, chartData.colors[i], chartData.pattern.patternColor);
                            options.data.datasets[0].borderColor[i] = chartData.pattern.color;
                            options.data.datasets[0].borderWidth[i] = 1;
                        }
                        else {
                            options.data.datasets[0].borderWidth[i] = 0;
                            options.data.datasets[0].backgroundColor[i] = chartData.colors[i];
                        }
                    }
                    else{
                        options.data.datasets[0].backgroundColor[i] = chartData.colors[i];
                        options.data.datasets[0].borderWidth[i] = 0;
                    }
                    options.data.datasets[0].data[i] = 50;
                }
            }
            //populate line charts
            else if(chartData.chartType == 'line'){
                for (var i = 0; i < chartData.labels.length; i++){
                    options.data.labels[i] = chartData.labels[i];
                    options.data.datasets[0].data[i] = 50;
                    options.data.datasets[1].data[i] = 50;
                }
            }
            //populate radar charts
            else{
                for (var i = 0; i < chartData.labels.length; i++){
                    options.data.labels[i] = chartData.labels[i];
                    options.data.datasets[0].pointBackgroundColor[i] = chartData.colors[0];
                    options.data.datasets[0].pointBorderColor[i] = chartData.borderColor;
                    options.data.datasets[0].data[i] = 50;
                    options.data.datasets[1].data[i] = chartData.data2[i];
                    options.data.datasets[1].pointBackgroundColor[i] = chartData.colors[1];
                    options.data.datasets[1].pointBorderColor[i] = chartData.borderColor;
                }
            }

            this.chart[num] = new Chart(ctx, options);
        },
    },
    watch:{
        check: function(val){
            if (this.check == true)
                this.initJsonLayers(this.checkLayer, this.checkJson, this.chart, this.layers[this.initial].chart,0);
            else
                this.checkLayer[0].remove(this.map);
        },
        counter: function(val){
          if (this.flowCanvas)
                this.flowCanvas.clearAllPathSelections();

          if (this.oms)
             this.oms.unspiderfy();

          this.$emit('resetToggle');

          this.removeLayers();
          this.initLayers();

        },
        spyglassID: function(val){
            if (this.spyglassLayer){
                this.spyglassLayer.remove(this.map);
                this.spyglassLayer = null;
                this.initSpyglass(this.spyglassID);
            }
            else
                this.initSpyglass(this.spyglassID);
        },
        //slider changes
        slide: function(val){
          for(var i = 0; i < this.json.length; i++){
                this.json[i].remove(this.map);
          }

          if (this.oms)
             this.oms.unspiderfy();

          if (this.layers[this.initial].slider)
                this.sliderValue = this.layers[this.initial].slider.default;

          if (this.layers[this.initial].jsonLayers){
            for(var i  = 0; i < this.layers[this.initial].jsonLayers.length;i++)
                this.initJsonLayers(this.json, this.layers[this.initial].jsonLayers[i], this.chart, this.layers[this.initial].chart,i);
          }

          if (this.layers[this.initial].tileSlider){
            for(var i = 0; i < this.tiles.length; i++){
              if (this.tiles[i])
                this.tiles[i].remove(this.map);
            }
            this.initTileLayers(this.layers[this.initial].tileLayers, true);
          }
        },
        featureSelected: function(val){
          if (this.featureSelected == false){
            this.removeLayers();
            this.initLayers();
            if (this.flowCanvas)
                this.flowCanvas.clearAllPathSelections();
          }
        },
        isToggled:function(val){
            if (val == true){
                this.removeLayers();
                this.initTileLayers(this.layers[this.initial].switchLayers);
            }
            else{
                this.removeLayers();
                this.initTileLayers(this.layers[this.initial].tileLayers);
            }
        }
    }
}
</script>

