<template>
    <div id="checklist" style="margin-bottom:15px; margin-top:15px;">
        <p id="checkText" style="padding-right:60px; position:relative;">{{toggleData.labels[0]}}</p>
            <label class="switch">
                <input name="checkbox_name" id="checkbox" type="checkbox" v-on:click="toggled()" autocomplete="off">
                <span id="switch" class="slider" v-bind:style="{background:toggleData.color}"></span>
            </label>
        <p id="checkText2" style="padding-left:5px;">{{toggleData.labels[1]}}</p><br/>
    </div>
</template>

<script>

export default {
  name:'ToggleSwitch',
  props:{
      toggleData:Object,
  },
  mounted(){
      $("#switch").css("left",this.leftText("#checkText"));
  },
  updated(){
      $("#switch").css("left",this.leftText("#checkText"));
      $('#checkbox').prop('checked', false);
  },
  methods:{
    leftText: function(id){
        var textWidth = $(id).width() + 5;
        return textWidth + 'px';
    },
    toggled: function(){
        var value = $('#checkbox').is(':checked') ? true : false;
        this.$emit('toggled', value);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
p {
  margin-bottom:0;
  margin-top:0;
  font-size:20px;
  font-weight:bold; 
  display:inline-block;
}

.switch input {display:none;}

.slider {
  position: absolute;
  cursor: pointer;
  height: 24px;
  width: 50px;
  left: 0;
  right: 0;
  -webkit-transition: .4s;
  transition: .4s;
  float:center;
}
.slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
</style>
