<template>
    <label class="toggleLayer" id="toggle">
		<input name="border_checkbox" id="checkbox" type="checkbox" value="check" v-on:click="checked()" autocomplete="off">
		<div class="toggleBorder"><!--ADDED HTML --><span class="on">X</span><span class="border">{{checkbox.title}}</span><!--END--></div>		
	</label>
</template>

<script>

export default {
  name:'LayerCheckbox',
  props:{
      checkbox:Object
  },
  methods:{
    checked: function(){
        var value = $('#checkbox').is(':checked') ? true : false;
        this.$emit('checked', value);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.toggleLayer {
  margin-top:5px;
  margin-bottom:5px;
  margin-left:50px;
  position: relative;
  display: inline-block;
  width: 175px;
  height: 30px;
  border: solid 2px black;
  box-shadow: 0 0 4px #999;
}

.toggleLayer input {display:none;}

.toggleBorder {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255,255,255,1);
  -webkit-transition: .4s;
  transition: .4s;
  float:center;
}

.toggleBorder:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 24px;
  left: 2px;
  bottom: 2px;
  background-color: rgba(255, 255, 255, 1);
  -webkit-transition: .4s;
  transition: .4s;
  border: solid 2px black;
}

input:checked + .toggleBorder {
  /*background-color: #000000;*/
}

input:focus + .toggleBorder {
  box-shadow: 0 0 1px #000000;
}

input:checked + .toggleBorder:before {
  background-color: rgba(230, 230, 230, 1);
}

.border
{
  color:#4d4d4d; 
  position: absolute;
  left: 40px;
  top:5px;
  font-size: 18px;
}

.on
{
  display:none;
  color:black; 
  position: absolute;
  left:9px;
  top:4px;
  font-size: 20px;
  font-weight:bold;
}

input:checked + .toggleBorder .on
{display: block;}
</style>
