<template>
    <div id="links" class="linkOverlay">
        <div class="linkPopup">
            <button @click="close()" class="close" style="display:inline;">×</button>
            <div class="content">
                <router-link v-bind:to="chapterMaps.chapterLink" style="text-decoration:none;">
                    <a style="font-weight:bold;">Chapter Home</a>
                </router-link>
                <hr>
                <router-link v-for="item in chapterMaps.maps" v-bind:key="item.index" v-bind:to="item.mapLink">
                    <a style="text-decoration:underline;">{{item.text}}</a>
                </router-link>
                <hr>
                <a href="/" style="font-weight:bold; text-decoration:none;">Atlas Home</a>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'LinkPopup',
  props:{
      chapterMaps:Object
  },
  methods:{
      decodeHtml(html){
        var txt = document.createElement("textarea");
        txt.innerHTML = html;
        return txt.value;
      },
      close(){
         this.$emit('close', false);
      }
  }
}
</script>
    
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.linkOverlay {
  position: fixed;
  bottom: 55px;
  left: 60px;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: visible;
  opacity: 1;
  z-index:1500;
  width:160px;
}

.linkPopup {
  padding:10px;
  background: #fff;
  width: 100%;
  position: relative;
  transition: all 5s ease-in-out;
  font-family:GoudyOld;
  height:100%;
  box-shadow: 3px 3px 2px #4d4d4d;
  border:2px black solid;
}

.linkPopup h2 {
  font-size:20px;
  margin-top: 0;
  color: #333;
  font-weight:bold;
}
.linkPopup a {
  display:block;
  font-size:20px;
  line-height:28px;
  color:#000000;
}
.linkPopup a:hover {
  color:#8c8c8c;
  cursor:pointer;
}

.linkPopup .close{
  top:5px;
  right:8px;
  position: absolute;
  transition: all 200ms;
  font-size: 20px;
  text-decoration: none;
  color: #a6a6a6;
  text-align:right;
  font-family:GoudyOld;
  background:none;
  border:none;
}
.linkPopup .close:hover {
  color: #737373;
  cursor:pointer;
}
</style>
