<template>
   <body v-if="pageData">
      <MapNavbar title="Atlas of the Pacific Northwest"/>
      <InfoPanel v-bind:data="pageData" v-bind:chapterMaps="chapterData" v-bind:selected="initial" v-bind:borders="borders"  @clicked="changeSelection" @filterClicked="changeFilter" @spyglassClicked="spyglassClicked" @sliderChange="changeSlider" @resetSelection="resetSelection" @checked="checkLayer" @toggled="toggled"/>
      <CollapseInfo v-if="pageData.maps[initial].slider == null || pageData.maps[initial].slider == false" divID="InfoPanel" v-intro-step="9" v-intro="'Select to collapse info panel.'"/>
      <BaseMap v-bind:chapter="pageData.chapter" v-bind:basemap="pageData.basemap" v-bind:bounds="pageData.bounds" v-bind:zoom="pageData.zoom" v-bind:center="pageData.center" v-bind:filterValue="filterValue" v-bind:isToggled="isToggled" v-bind:layers="pageData.maps" v-bind:check="isChecked" v-bind:initial="initial" v-bind:basemapType="pageData.basemapType" v-bind:counter="counter" v-bind:checkJson="pageData.checkboxLayer" v-bind:secondDropdownButtons="pageData.secondDropdown" v-bind:featureSelected="pageData.maps[initial].featureSelected" v-bind:slide="defaults.slide" v-bind:spyglass="pageData.spyglass" v-bind:spyglassID="spyglass" @featureSelected="featureSelected" @resetToggle="resetToggle" @borders="borderChange" v-intro-step="11" v-intro="'This is the main map display. Hover over the map data to reveal more information.'"/>
   </body>
</template>

<script>

import BaseMap from '../components/map/BaseMap.vue'
import CollapseInfo from '../components/map/CollapseInfo.vue'
import InfoPanel from '../components/map/InfoPanel.vue'
import MapNavbar from '../components/map/MapNavbar.vue'

const L = window.L;
export default {
  name:'Map',
  components:{
    BaseMap,
    CollapseInfo,
    InfoPanel,
    MapNavbar
  },
  data() {
    return {
      defaults: {
          slide:1,
      },
      pageData: null,
      chapterData: null,
      initial:0,
      counter:0,
      spyglass:1,
      filterValue:null,
      isChecked:false,
      isToggled:false,
      borders:null
    }  
  },
  created(){
      var maskLayer = document.createElement('script');
            maskLayer.type = 'text/javascript';
            maskLayer.src = "js/leaflet-tilelayer-mask.js";    
            document.getElementsByTagName('head')[0].appendChild(maskLayer);
      this.fetchData();
      this.fetchMapData();
  },
  watch:{
      '$route':'fetchMapData'
  },
  methods:{
      addScripts(){
          for (var i = 0; i < this.pageData.maps.length; i++){///////TO DO////////
            if (this.pageData.maps[i].jsonLayers){
                for (var j = 0; j < this.pageData.maps[i].jsonLayers.length; j++){
                    if (this.pageData.maps[i].jsonLayers[j].feature == 'heat'){                    
                        console.log("addScript");
                        var script = document.createElement('script');
                            script.type = 'text/javascript';
                            script.src = this.pageData.maps[i].jsonLayers[j].url;    
                            script.async = true;
                        document.getElementsByTagName('head')[0].appendChild(script);
                    }
                }
            }
        }
      },
      checkLayer(value){
          this.isChecked = value;
      },
      changeSelection(value){
          this.initial = value;
          this.counter++;
          this.resetSelection();
      },
      changeSlider(value){
          if (this.pageData.maps[this.initial].slider){
              this.pageData.maps[this.initial].slider.default = value;
              this.defaults.slide++;
          }
      },
      changeFilter(value){
            this.filterValue = value;
            this.counter++;
      },
      spyglassClicked(value){
            this.spyglass = value;
      },
      featureSelected(){
          this.pageData.maps[this.initial].featureSelected = true;
      },
      resetSelection(){
        this.pageData.maps[this.initial].featureSelected = false;
        $('.selectionContainer').css('display','none');
        $('.selectionChartOnlyContainer').css('display','none');
        
        for (var k = 0; k < this.pageData.maps[this.initial].legend.length; k++){
            var isSelectionTitle = document.getElementById("selectionTitle-"+this.initial);
            if (this.pageData.maps[this.initial].legend[k].type == 'selection' || this.pageData.maps[this.initial].legend[k].type == 'selectionChart' || this.pageData.maps[this.initial].legend[k].type == 'selectionChartOnly')
                $('#selectionTitle-'+this.initial).html(this.pageData.maps[this.initial].legend[k].title);
        }

        var isSelectedDiv = document.getElementsByClassName("selected");
        var isUnSelectedDiv = document.getElementsByClassName("unselected");
        if (isUnSelectedDiv.length != 0){
            for (var k = 0; k < isUnSelectedDiv.length; k++)
                isUnSelectedDiv[k].style.display = "block";
        }
        if (isSelectedDiv.length != 0){
            for (var k = 0; k < isSelectedDiv.length; k++)
                isSelectedDiv[k].style.display = "none";
        }     
    },
    fetchData(){
        let self = this;  

        var fileName = "./page_data/" + this.$route.params.name + "/" + this.$route.params.name + ".json";
        const myRequest = new Request(fileName);

        fetch(myRequest)
          .then( res => res.json())
          .then(data => {
              self.chapterData = data;
              console.log('Request successful', data);
          })
          .catch(function(error) {
            console.log('Request failed', error)
          });
     },
     fetchMapData(){
        let self = this;  
        this.pageData = null;
        this.counter = 0;

        var fileName = "./page_data/" + this.$route.params.name + "/" + this.$route.params.map + ".json";
        const myRequest = new Request(fileName);

        fetch(myRequest)
          .then( res => res.json())
          .then(data => {
              self.pageData = data;
              console.log('Request successful', data);
              this.changeSelection(0);
              document.title = self.pageData.title;
              $('meta[property="og:title"]').remove();
              $('head').append( '<meta property="og:title" content="">');
              $("meta[property='og:title']").attr("content", "Atlas of the Pacific Northwest: " + self.pageData.title);
          })
          .catch(function(error) {
            console.log('Request failed', error)
          });
     },
     toggled: function(value){
        this.isToggled = value;
    },
    resetToggle: function(){
        this.isToggled = false;
    },
    borderChange: function(value){
        this.borders = value;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
     @import "../../node_modules/intro.js/introjs.css";

    body{
      height:100%;
      width:100%;
    }
    .customDefault {
        font-family:GoudyOld;
        border: 2px solid black;
    }
    .customDefault .introjs-nextbutton, .customDefault .introjs-prevbutton, .customDefault .introjs-skipbutton, .customDefault .introjs-donebutton{
        font-family:GoudyOld;
        font-size:14;
    }
</style>
