import Vue from 'vue'
import VueRouter from 'vue-router'
import VueIntro from 'vue-introjs'
import App from './App.vue'

Vue.use(VueRouter)
Vue.use(VueIntro)

import router from './router'

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')

